<template>
  <div>
    <v-card-text>
      <v-row :key="index" v-for="(item, index) of items">
        <v-col class="wrap-remove" cols="1">
          <v-checkbox v-model="item.enable" />
        </v-col>
        <v-col v-if="type === 'globalVariable'" class="wrap-remove" cols="2">
          <v-checkbox label="Use Default?" v-model="item.useDefault" />
        </v-col>
        <v-col cols="3">
          <v-text-field v-if="!type || type !== 'header'" v-model="item.key" label="Key" ></v-text-field>
          <v-combobox style="text-align: left" v-if="type === 'header'" v-model="item.key" :items="headerKeys" :label="'Key'" persistent-hint></v-combobox>
        </v-col>
        <v-col cols="3">
          <v-text-field
            :type="item.type === 'number' ? item.type: ''"
            v-if="(!type || type !== 'header') && item.type !== 'boolean'"
            v-model="item.value"
            label="Value" ></v-text-field>
          <v-combobox
            :type="item.type === 'number' ? item.type: ''"
            v-if="type === 'header' && item.type !== 'boolean'" v-model="item.value"
            :items="headerValues" :label="'Value'"
            persistent-hint></v-combobox>
          <v-select :label="'Value'" v-if="item.type === 'boolean'" v-model="item.value" :items="['true', 'false']"></v-select>
        </v-col>
        <v-col cols="2">
          <v-select @change="(v) => onChangeType(v, item)" v-model="item.type" :items="['string', 'number', 'boolean']"></v-select>
        </v-col>
        <v-col class="wrap-remove" cols="1">
          <v-btn icon @click="clickRemove(index)">
            <v-icon>mdi-close-circle</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-card-text>
    <v-card-actions>
      <v-btn block small @click="clickAdd">
        Add
      </v-btn>
    </v-card-actions>
  </div>
</template>

<script>
export default {
  data: () => ({
    headerKeys: [
      'Accept', 'Accept-Charset', 'Accept-Encoding',
      'Accept-Language', 'Accept-Datetime',
      'Access-Control-Request-Method', 'Access-Control-Request-Headers',
      'Authorization', 'Cache-Control', 'Connection',
      'Content-Length', 'Content-Type', 'Cookie',
      'Date', 'Expect', 'Forwarded', 'From', 'Host',
      'If-Match', 'If-Modified-Since', 'If-None-Match',
      'If-Range', 'If-Unmodified-Since', 'Max-Forwards',
      'Origin', 'Pragma', 'Proxy-Authorization', 'Range',
      'Referer', 'TE', 'User-Agent', 'Upgrade', 'Via', 'Warning'
    ],
    headerValues: [
      'application/atom+xml', 'application/ecmascript',
      'application/json', 'application/vnd.api+json',
      'application/javascript', 'application/octet-stream',
      'application/ogg', 'application/pdf', 'application/postscript',
      'application/rdf+xml', 'application/rss+xml', 'application/soap+xml',
      'application/font-woff', 'application/x-yaml', 'application/xhtml-xml',
      'application/xml', 'application/xml-dtd', 'application/xop+xml',
      'application/zip', 'application/gzip', 'application/graphql',
      'application/x-www-form-urlencoded', 'audio/basic', 'audio/L24',
      'audio/mp4', 'audio/mpeg', 'audio/ogg', 'audio/vorbis',
      'audio/vnd.rn-realaudio', 'audio/vnd.wave', 'audio/webm',
      'image/gif', 'image/jpeg', 'image/svg+xml', 'image/tiff',
      'message/http', 'message/imdn+xml', 'message/partial',
      'message/rfc822', 'multipart/mixed', 'multipart/alternative',
      'multipart/related', 'multipart/form-data', 'multipart/signed',
      'multipart/encrypted', 'text/cmd', 'text/css', 'text/csv',
      'text/html', 'text/plain', 'text/vcard', 'text/xml'
    ]
  }),
  props: ['items', 'type'],
  methods: {
      onChangeType(v, item) {
          if (v === 'string' && item.value) {
              item.value = item.value.toString();
          }
      },
      clickAdd() {
        this.items.push({enable: true, type: 'string'});
      },
      clickRemove(index) {
        this.items.splice(index, 1);
      }
  }
};
</script>
<style>
  .v-card__actions {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .wrap-remove {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .v-autocomplete__content.v-menu__content {
    text-align: left;
  }
</style>
