import axios from 'axios';
import { getUrl } from './utils/utils';

const result = axios.create({
  headers: {
    'Content-Type': 'application/json',
  }
});
result.interceptors.request.use(function (config) {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers['Authorization'] = 'Bearer ' + token;
  }
  const port = 1414;
  config.baseURL = `${getUrl()}:${port}/api`;
  return config;
}, function (error) {
  return Promise.reject(error);
});

result.interceptors.response.use(function ({ data }) {
  return data;
}, function (error) {
  const { response, config } = error;
  if (response) {
    const { status, data } = response;
    if (status === 401) {
      localStorage.clear();
      window.location.href = 'login';
    }
    if (status === 403) {
      window.location.href = '/exception-403';
    }
    if(!config.restrictToast) {
      window.EventBus.$emit('show-toast', data.message);
    }
    window.EventBus.$emit('change-loading', false);
    const logs = JSON.parse(localStorage.getItem('logs')) || [];
    const log = {time: new Date().getTime(), message: data.message};
    if (data.description) {
      log.description = data.description;
    }
    const existingLogs = logs.find(l => {
      let result = l.message === data.message;
      if (l.description) {
        result &= l.description === data.description;
      }
      return result;
    });
    if (!existingLogs || existingLogs.length === 0) {
      logs.push(log);
    }
    localStorage.setItem('logs', JSON.stringify(logs));
    return Promise.reject(data);
  }
  return Promise.reject(error);
});

export default result;
