<template>
  <div>
    <div class="text-left" style="padding: 20px 0;">
      <v-btn x-small color="primary" @click="clickImport('collections')">
        Import&nbsp;<v-icon small>mdi-import</v-icon>
      </v-btn>
      <v-btn x-small @click="() => clickJsonExport(collections)" color="#009688">
        Export &nbsp;<v-icon small>mdi-export</v-icon>
      </v-btn>
      <v-btn x-small @click="showVariableDialog" color="success">
        Variables&nbsp;<v-icon small>mdi-variable</v-icon>
      </v-btn>
      <v-btn
        @click="confirmDialog = true; action = 'sendAll'"
        x-small color="warning">
          Execute All Global&nbsp;<v-icon small>mdi-home-automation</v-icon>
      </v-btn>
      <v-btn x-small @click="saveAll" color="info"> Save All Global </v-btn>
    </div>
    <v-tabs vertical v-model="collectionTab" class="collection-tabs">
      <div style="width: 280px">
        <draggable v-model="collections" @start="drag = true" @end="onDropCollection" class="left-panel">
          <v-tab
            @contextmenu="(e) => showCollectionMenu(e, index)"
            :key="index" @click.native="() => clickOnCollectionTab(index)"
            v-for="(collection, index) of collections">
            {{ collection.name }}
            <v-spacer></v-spacer>
            <v-btn @click="event => removeCollection(index, event)" class="btn-delete" icon x-small color="warning" >
              <v-icon x-small>mdi-close-circle</v-icon>
            </v-btn>
          </v-tab>
          <v-tab :key="collections.length" class="api-tab tab-add-collection" @click.native="addCollection">
            <v-icon>mdi-plus</v-icon>
          </v-tab>
        </draggable>
      </div>
      <div style="width: calc(100vw - 380px);">
        <v-card style="min-height: calc(100vh - 24px)" class="right-panel">
          <v-card-text>
            <v-tabs v-model="tab">
              <draggable
                v-if="collections[collectionTab]"
                v-model="collections[collectionTab].items"
                @start="drag = true" @end="() => onEnd(collectionTab)">
                <v-tab
                  :style="{ color: item.enable ? '#1976d2' : 'gray' }"
                  class="api-tab text-left" :key="index"
                  v-for="(item, index) of collections[collectionTab].items"
                  @contextmenu="(e) => showTabMenu(e, collectionTab, index)"
                >
                  <span v-bind:style="{ color: methodColor[item.method] }" class="text-method">{{ item.method }}</span>&nbsp;
                  <span>{{ item.name }}</span>

                  <v-btn @click.native="event => removeItem(collectionTab, index, event)" class="btn-delete" icon x-small color="warning" >
                    <v-icon x-small>mdi-close-circle</v-icon>
                  </v-btn>
                </v-tab>
                <v-tab class="api-tab" @click.native="addItem(collectionTab)" :key="collections[collectionTab].items.length">
                  <v-icon>mdi-plus</v-icon>
                </v-tab>
              </draggable>
            </v-tabs>
            <v-tabs-items v-if="collections[collectionTab]" v-model="tab">
              <v-tab-item :key="index" v-for="(item, index) of collections[collectionTab].items">
                <v-card flat>
                  <v-card-text>
                    <api-client-tab
                      :send="item => sendRequest(collectionTab, index)"
                      :save="item => onClickSave(item, collectionTab, index)"
                      :resp="resps[collections[collectionTab]._id] ? resps[collections[collectionTab]._id][index] : ''"
                      :item="item"
                      >
                    </api-client-tab>
                  </v-card-text>
                </v-card>
              </v-tab-item>
            </v-tabs-items>
          </v-card-text>
        </v-card>
      </div>
    </v-tabs>

    <v-dialog v-model="importDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Import</span>
        </v-card-title>
        <v-card-text>
          <v-alert class="text-left" dense colored-border type="error" elevation="2" v-show="disableSave && dataImport" >Incorrect Data Type!</v-alert >
          <v-container>
            <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport" @input="onChangeImportData" ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialog">Cancel</v-btn>
          <v-btn color="blue darken-1" :disabled="disableSave" text v-on:click="saveImport">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="collectionDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Collection Setting</span>
        </v-card-title>
        <v-card-text>
          <v-text-field placeholder="Name" v-model="editableCollection.name" label="Name" >
          </v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialog">Cancel</v-btn>
          <v-btn color="blue darken-1" text v-on:click="saveCollection">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="variablesDialog" max-width="1200px">
      <v-card>
        <v-card-title>
          <span class="headline">Global Variables</span>
        </v-card-title>
        <v-card-text> <key-value-form :type="'globalVariable'" :items="variables"></key-value-form> </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialog">Cancel</v-btn>
          <v-btn color="blue darken-1" text v-on:click="saveVariables">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="tabVariableDialog" max-width="800px">
      <v-card>
        <v-card-title>
          <span class="headline">Tab: {{itemName}} Variables</span>
        </v-card-title>
        <v-card-text> <key-value-form :type="'tabVariable'" :items="tabVariables"></key-value-form> </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="cancelVariableDialog">Cancel</v-btn>
          <v-btn color="blue darken-1" text v-on:click="sendTabVariables">Send</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="tabDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Tabs Setting</span>
        </v-card-title>
        <v-card-text>
          <v-text-field placeholder="Name" label="Name" v-model="editableTab.name"></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialog">Cancel</v-btn>
          <v-btn color="blue darken-1" text v-on:click="saveTab" >Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="sending"
      hide-overlay
      persistent
      width="300"
    >
      <v-card color="primary" dark>
        <v-card-text style="padding: 5px">
          Sending {{itemName}}
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
          <v-btn x-small color="darken-1" text @click="cancelTab = true">Cancel</v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog v-model="confirmDialog" persistent max-width="290" >
      <v-card>
        <v-card-title class="headline">
          Confirm?
        </v-card-title>
        <v-card-text>Are you sure?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="confirmDialog = false" >
            No
          </v-btn>
          <v-btn color="red darken-1" text @click="onConfirm">
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-menu transition="slide-y-transition" bottom v-model="tabMenu" :position-x="tabX" :position-y="tabY" >
      <v-list class="text-left">
        <v-list-item link @click.native="showTabSetting">
          <v-icon small>mdi-square-edit-outline</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Edit </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="clickImport('item')">
          <v-icon small>mdi-import</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Import </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="() => clickJsonExport(collections[collectionIndex].items[selectedTabIndex])">
          <v-icon small>mdi-export</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Export </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="() => sendRequest(collectionIndex, selectedTabIndex)">
          <v-list-item-title class="text-small">
            <v-icon small>mdi-home-automation</v-icon>&nbsp;&nbsp;
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <span v-bind="attrs" v-on="on"> Send </span>
              </template>
              <span>Send request</span>
            </v-tooltip>
          </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="onClickSave(
          collections[collectionIndex].items[selectedTabIndex],
          collectionIndex, selectedTabIndex
        )">
          <v-list-item-title class="text-small">
            <v-icon small>mdi-content-save</v-icon>&nbsp;&nbsp;
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <span v-bind="attrs" v-on="on"> Save </span>
              </template>
              <span>Save this collection</span>
            </v-tooltip>
          </v-list-item-title>
        </v-list-item>

        <v-list-item link @click.native="closeOtherTabs">
          <v-icon small>mdi-close-circle</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Close others </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
    <v-menu transition="slide-y-transition" bottom v-model="collectionMenu" :position-x="collectionX" :position-y="collectionY">
      <v-list class="text-left">
        <v-list-item link @click="evt => showCollectionSetting(collectionMenuIndex, evt)">
          <v-icon small>mdi-square-edit-outline</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Edit Tab Name </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="clickImport('items', collectionMenuIndex)">
          <v-icon small>mdi-import</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Import All Tabs </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="() => clickJsonExport(collections[collectionMenuIndex].items)">
          <v-icon small>mdi-export</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Export All Tabs </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="sendAllTabs(collectionMenuIndex)">
          <v-list-item-title class="text-small">
            <v-icon small>mdi-home-automation</v-icon>&nbsp;&nbsp;
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <span v-bind="attrs" v-on="on"> Execute All Tabs </span>
              </template>
              <span>Send all request in this collection</span>
            </v-tooltip>
          </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="() => saveData(collections[collectionMenuIndex])">
          <v-list-item-title class="text-small">
            <v-icon small>mdi-content-save</v-icon>&nbsp;&nbsp;
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <span v-bind="attrs" v-on="on"> Save All Tabs </span>
              </template>
              <span>Save this collection</span>
            </v-tooltip>
          </v-list-item-title>
        </v-list-item>
        <v-list-item link @click.native="closeOtherCollections">
          <v-icon small>mdi-close-circle</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Close others </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </div>
</template>

<script>
  import ApiClientTab from './ApiClientTab';
  import KeyValueForm from './KeyValueForm.vue';
  import draggable from 'vuedraggable';
  import { copyToClipboard, isJsonString, delay, getUrl, getVariableName } from '../utils/utils';
  import axios from '../axios';
  import _axios from 'axios';
  const defaultItem = { name: 'New Tab', method: 'Get', params: [], headers: [], body: [], enable: true };
  export default {
    components: {
      ApiClientTab,
      KeyValueForm,
      draggable,
    },
    data: () => ({
      variablesDialog: false,
      importDialog: false,
      collectionDialog: false,
      tabVariableDialog: false,
      tabDialog: false,
      confirmDialog: false,
      tabMenu: false,
      collectionMenu: false,
      disableSave: true,
      methodColor: {Get: 'green', Post: 'orange', Put: 'lightcoral', Patch: 'gray', Delete: 'red'},
      action: '',
      sending: false,
      itemName: '',
      cancelTab: false,
      editableCollection: {},
      editableTab: {},

      tabVariables: [],
      variables: [],
      dataImport: '',
      collectionIndex: -1,
      collectionMenuIndex: -1,
      selectedTabIndex: 0,
      varItemIndex: -1,
      itemIndex: -1,
      tab: 0,
      collectionTab: 0,
      importType: '',
      resps: {},
      collections: [{ name: 'New', items: [defaultItem] }],
      defaultCollections: [{ name: 'New', items: [defaultItem] }],

      collectionX: 0,
      collectionY: 0,
      tabX: 0,
      tabY: 0,
    }),
    mounted() {
      this.getData();
      const { setting } = this.$store.state;
      this.variables = setting.variables || [];
    },
    methods: {
      async closeOtherCollections() {
          let index = 0;
          for (const c of this.collections) {
              if (this.collectionMenuIndex !== index) {
                  await this.deleteData(c._id);
              }
              index++;
          }
          this.getData();
      },
      async closeOtherTabs() {
          let index = 0;
          const items = [];
          for (const item of this.collections[this.collectionIndex].items) {
              if (this.selectedTabIndex === index) {
                  items.push({...item});
              }
              index++;
          }
          this.collections[this.collectionIndex].items = [...items];
          await this.saveData(this.collections[this.collectionIndex]);
      },
      async cancelVariableDialog() {
        this.tabVariableDialog = false;
        if (this.action !== '') {
          await this[this.action]();
        }
      },
      async saveTab() {
          const item = this.collections[this.collectionIndex].items[this.selectedTabIndex];
          this.collections[this.collectionIndex].items[this.selectedTabIndex] = {...item, ...this.editableTab};
          await this.saveData(this.collections[this.collectionIndex]);
          this.collectionIndex = -1;
      },
      async sendTabVariables() {
        for (const tabVariable of this.tabVariables) {
          if (!tabVariable.value) {
            this.$toasted.show('Please fill all data to continue', {type: 'error'});
            return;
          }
        }
        const item = this.collections[this.collectionIndex].items[this.varItemIndex];
        await this.send(item, this.varItemIndex);
        this.tabVariableDialog = false;
        if (this.action !== '') {
          await this[this.action]();
        }
      },
      clickOnCollectionTab(index) {
        this.tab = 0;
        this.collectionTab = index;
      },
      async sendAll() {
        for (let i = 0; i < this.collections.length; i++) {
          if (i < this.collectionIndex || (i === this.collectionIndex && this.itemIndex === this.collections[i].items.length)) {
            this.itemIndex = -1;
            continue;
          }
          this.collectionIndex = i;
          const result = await this.sendAllInCollection();
          if (!result) {
            this.action = 'sendAll';
            return;
          }
        }
        this.collectionIndex = -1;
        this.itemIndex = -1;
        this.varItemIndex = -1;
      },
      async sendAllInCollection() {
        const { items } = this.collections[this.collectionIndex];
        let index = 0;
        this.resps[this.collectionIndex] = {};
        for (const item of items) {
          if (!item.enable || !item.url || this.cancelTab || this.varItemIndex >= index) {
            index++;
            continue;
          }
          this.itemIndex++;
          this.tabVariables = this.checkVariable(item);
          if (this.tabVariables.length > 0) {
            this.tabVariableDialog = true;
            this.itemName = item.name;
            this.varItemIndex = index;
            return false;
          }
          await this.send(item, index);
          index++;
        }
        this.varItemIndex = -1;
        return true;
      },
      onConfirm() {
        this.confirmDialog = false;
        this.cancelTab = false;
        this[this.action]();
      },
      showCollectionSetting(index, evt) {
        this.collectionMenu = false;
        this.collectionIndex = index;
        this.collectionDialog = true;
        this.editableCollection = {name: this.collections[index].name};
        evt.stopPropagation();
      },
      showTabSetting(evt) {
        this.tabDialog = true;
        this.editableTab = {name: this.collections[this.collectionIndex].items[this.selectedTabIndex].name};
        evt.stopPropagation();
        this.tabMenu = false;
      },
      async onDropCollection() {
        this.drag = false;
        let index = 0;
        for (const collection of this.collections) {
          collection.index = index;
          await this.saveData(collection);
          index++;
        }
      },
      async onEnd(collectionTab) {
        this.drag = false;
        let index = 0;
        for (const item of this.collections[collectionTab].items) {
          item.index = index;
          index++;
        }
        await this.saveData(this.collections[collectionTab]);
      },
      async saveCollection() {
          this.collections[this.collectionIndex] = {...this.collections[this.collectionIndex], ...this.editableCollection};
          await this.saveData(this.collections[this.collectionIndex]);
          this.collectionIndex = -1;
      },
      async saveAll() {
        for (const collection of this.collections) {
          await this.saveData(collection);
        }
        this.getData();
      },
      sendAllTabs(index) {
        this.confirmDialog = true;
        this.collectionIndex = index;
        this.action = 'sendAllInCollection';
      },
      checkVariable(item) {
        let variableKeys = [];
        const { variables: totalVariables } = this.$store.state.setting;
        const variables = {};

        totalVariables.filter(v => v.enable).forEach(v => {
          if (v.key && v.value) {
            variables[v.key] = v;
          }
        });

        for (const data of item.headers) {
          if (data.key && data.value && data.enable) {
            const keys = getVariableName(data.key);
            const values = getVariableName(data.value);
            for (const key of keys) {
              variableKeys.push(key);
            }
            for (const value of values) {
              variableKeys.push(value);
            }
          }
        }

        for (const data of item.params) {
          if (data.key && data.value && data.enable) {
            const keys = getVariableName(data.key);
            const values = getVariableName(data.value);
            for (const key of keys) {
              variableKeys.push(key);
            }
            for (const value of values) {
              variableKeys.push(value);
            }
          }
        }

        const urlVariables = getVariableName(item.url);

        for (const key of urlVariables) {
          variableKeys.push(key);
        }

        for (const data of item.body) {

          if (data.key && data.value && data.enable) {
            const keys = getVariableName(data.key);
            const values = getVariableName(data.value);
            for (const key of keys) {
              variableKeys.push(key);
            }
            for (const value of values) {
              variableKeys.push(value);
            }
          }
        }

        const result = [];

        variableKeys = variableKeys.filter(function(item, pos) { return variableKeys.indexOf(item) == pos });
        for (const variableKey of variableKeys) {
          if (variables[variableKey] && variables[variableKey].useDefault) {
            continue;
          }
          let variableValue = '';
          let variableType = 'string';
          if (variables[variableKey] && variables[variableKey].value) {
            variableValue = variables[variableKey].value;
            variableType = variables[variableKey].type;
          }
          result.push({key: variableKey, value: variableValue, type: variableType, enable: true})
        }
        return result;
      },
      getVarValue(str) {
        let { variables: settingVarables } = this.$store.state.setting;
        const tabVariableKeys = this.tabVariables.map(t => {
            if (t.enable) {
                return t.key;
            }
        });
        settingVarables = settingVarables.filter(s => {
            return s.key && s.value && s.enable && !tabVariableKeys.includes(s.key);
        });
        const variables = [...this.tabVariables, ...settingVarables];
        const variableObj = {};
        if (variables) {
          for (const variable of variables) {
            if (variable.key && variable.value && variable.enable) {
              variableObj[variable.key] = variable;
            }
          }
        }
        const varNames = getVariableName(str);
        for (const varName of varNames) {
          if (variableObj[varName]) {
            let value = variableObj[varName].value;
            if (variableObj[varName].type === 'boolean') {
              value = value === 'true';
            }
            if (variableObj[varName].type === 'number') {
              value = Number(value);
            }

            str = str.replace('${' + varName + '}', value);
          }
        }
        return str;
      },
      async sendRequest(collectionIndex, index) {
        this.collectionIndex = collectionIndex;
        const item = this.collections[collectionIndex].items[index];
        if (!this.resps[collectionIndex]) {
          this.resps[collectionIndex] = {};
        }
        this.tabVariables = this.checkVariable(item);
        if (this.tabVariables.length > 0) {
          this.tabVariableDialog = true;
          this.itemName = item.name;
          this.varItemIndex = index;
          return false;
        }
        await this.send(item, index);
        this.collectionIndex = -1;
      },
      async send(item, index) {
        const headers = {};
        for (const header of item.headers) {
          if (header.key && header.value && header.enable) {
            const key = this.getVarValue(header.key);
            const value = this.getVarValue(header.value);
            headers[key] = value;
          }
        }

        const params = {};
        for (const param of item.params) {
          if (param.key && param.value && param.enable) {
            const key = this.getVarValue(param.key);
            const value = this.getVarValue(param.value);
            params[key] = value;
          }
        }
        const body = {};
        for (const bodyItem of item.body) {
          if (bodyItem.key && bodyItem.value && bodyItem.enable) {
            let value = bodyItem.value;
            if (bodyItem.type === 'boolean') {
              value = value === 'true';
            }
            if (bodyItem.type === 'string') {
              value = this.getVarValue(value);
            }
            if (bodyItem.type === 'number') {
              value = Number(value);
            }
            const key = this.getVarValue(bodyItem.key);
            body[key] = value;
          }
        }
        const url = this.getVarValue(item.url);
        const method = item.method.toLocaleLowerCase();
        const data = { url, method, headers, body, params };
        const backendPort = 1414;
        const token = localStorage.getItem('token');
        const config = { headers: {
            'Authorization': 'Bearer ' + token
        }};

        this.sending = true;
        this.itemName = item.name;
        const delayValue = item.delay ? item.delay*1000: 0;
        const collectionId = this.collections[this.collectionIndex]._id;
        try {
          await delay(delayValue);
          const {data: respData} = await _axios.post(`${getUrl()}:${backendPort}/api/api-client/send`, data, config);
          if (this.resps[collectionId]) {
            this.resps[collectionId][index] = respData;
          } else {
            this.resps[collectionId] = {[index]: respData}
          }
          this.$toasted.show(`${item.name} success!`);
        } catch ({response: {data: errData}}) {
          if (this.resps[collectionId]) {
            this.resps[collectionId][index] = errData;
          } else {
            this.resps[collectionId] = {[index]: errData}
          }
          this.$toasted.show(`${item.name} failed!`, { type: 'error' });
        }
        this.sending = false;
      },
      async onClickSave(item, collectionIndex, itemIndex) {
        this.collections[collectionIndex].items[itemIndex] = item;
        const r = await this.saveData(this.collections[collectionIndex]);
        await this.getData();
        this.$toasted.show(r.status);
      },
      saveVariables() {
        const { setting } = this.$store.state;
        setting.variables = this.variables;
        this.$store.commit('changeLoading', true);
        axios.put(`setting`, setting).then(result => {
          this.$store.commit('changeLoading', false);
          if (result === 'successful') {
            this.$store.commit('changeSetting', setting);
            this.$toasted.show('Success!');
            this.variablesDialog = false;
          }
        });
      },
      showVariableDialog() {
        this.variablesDialog = true;
      },
      async onChangeImportData() {
        this.disableSave = !isJsonString(this.dataImport);
      },
      clickJsonExport(data) {
        const status = copyToClipboard(JSON.stringify(data));
        const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
        const type = status ? `success` : `error`;
        return this.$toasted.show(msg, { type: type });
      },
      clickImport(type, index) {
        this.importDialog = true;
        this.importType = type;
        if (type === 'items') {
          this.collectionIndex = index;
        }
      },
      async saveImport() {
        if (this.importType === 'items') {
          const items = JSON.parse(this.dataImport);
          this.collections[this.collectionIndex].items = [
            ...this.collections[this.collectionIndex].items,
            ...items,
          ];
          this.saveData(this.collections[this.collectionIndex]);
        }
        if (this.importType === 'collections') {
          const collections = JSON.parse(this.dataImport);
          for (let i = 0; i < collections.length; i++) {
            const data = { ...collections[i] };
            delete data._id;
            this.saveData(data);
          }
        }
        if (this.importType === 'item') {
          const item = JSON.parse(this.dataImport);
          this.onClickSave({...item}, this.collectionIndex, this.selectedTabIndex)
        }
        this.importDialog = false;
        this.getData();
      },
      closeDialog() {
        this.variablesDialog = false;
        this.importDialog = false;
        this.collectionDialog = false;
        this.tabDialog = false;
        this.collectionIndex = -1;
      },
      async removeItem(collectionIndex, index, event) {
        const collection = this.collections[collectionIndex];
        collection.items.splice(index, 1);
        this.saveData(collection);
        if (event) {
            event.stopPropagation();
        }
      },
      async removeCollection(index, e) {
        const collection = this.collections[index];
        if (collection._id) {
          await this.deleteData(collection._id);
        }
        this.collections.splice(index, 1);
        e.stopPropagation();
      },
      async deleteData(id) {
        const r = await axios.delete(`api-client/${id}`);
        this.$toasted.show(r.status);
      },
      async saveData(data) {
        const r = await axios.put('api-client', data);
        this.closeDialog();
        return r;
      },
      addItem(index) {
        const item = JSON.parse(JSON.stringify(defaultItem));
        this.collections[index].items.push(item);
        this.tab = this.collections[index].items.length - 1;
      },
      async addCollection(e) {
        const collection = JSON.parse(JSON.stringify(this.defaultCollections[0]));
        this.collections.push(collection);
        this.collectionTab = this.collections.length - 1;
        this.tab = 0;
        await this.saveAll();
      },
      async getData() {
        const collections = await axios.get('api-client');
        for (const collection of collections) {
          if (!collection.items) {
            collection.items = [defaultItem].slice();
          }
        }
        this.collections = collections.length === 0 ? [...this.defaultCollections] : collections;
      },
      showTabMenu (e, collectionIndex, tabIndex) {
        this.collectionIndex = collectionIndex;
        this.selectedTabIndex = tabIndex;
        e.preventDefault();
        this.tabMenu = false;
        this.collectionMenu = false;
        this.tabX = e.clientX;
        this.tabY = e.clientY;
        this.$nextTick(() => {
          this.tabMenu = true
        })
      },
      showCollectionMenu (e, index) {
          e.preventDefault();
          this.collectionMenuIndex = index;
          this.collectionMenu = false;
          this.tabMenu = false;
          this.collectionX = e.clientX;
          this.collectionY = e.clientY;
          this.$nextTick(() => {
              this.collectionMenu = true
          })
      },
    },
  };
</script>
