    position: absolute !important;
    right: 2px;
    top: 2px;
  }
  .btn-edit {
    position: absolute !important;
    right: 20px;
    top: 2px;
  }
  .api-tab {
    float: left;
    height: 100%;

    min-width: 200px !important;
  }
  .tab-add-collection {
    width: 100%;
  }
  .left-panel {
    margin: 0px 2px;
  }
  #no-background-hover::before {
    background-color: transparent !important;
  }
  .text-small {
    font-size: small !important;
  }
  .text-method {
    font-size: 8px;
    font-weight: bold;
  }
  .collection-tabs .v-tabs-bar__content {
    flex-direction: initial !important;
  }
</style>
