<template>
  <div>

    <v-card style="padding: 30px">
      <v-card-title>
        <span class="headline">DEVICE DETAILS FORM</span>
        <v-spacer></v-spacer>
        <v-btn @click="generateUuid" x-small color="#FF9800" class="white--text" >
            Generate UUID
        </v-btn>&nbsp;
        <v-btn @click="importDialog = true" x-small color="primary" class="white--text">
          Import<v-icon x-small right dark>mdi-import</v-icon>
        </v-btn>&nbsp;
        <v-btn @click="clickJsonExport" x-small color="#009688" class="white--text" >
          Export<v-icon x-small right dark :disabled="!item.device_id"> mdi-export </v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text>
        <v-form v-model="valid" ref="form" lazy-validation>
          <v-row>
            <v-col cols="12" sm="12" md="5">
              <h3 class="white--text">Device</h3>
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field outlined dense label="Device Id" v-model="item.device_id" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field outlined dense label="Device Name" v-model="item.device_name" required :rules="[rules.required]" ></v-text-field>
                </v-col>
              </v-row>
              <h3 class="white--text">Customer Details</h3>
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field outlined dense label="Client Id" v-model="item.client_id" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field outlined dense label="Client Name" v-model="item.client_name" required :rules="[rules.required]" ></v-text-field>
                </v-col>
              </v-row>
            </v-col>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-col cols="12" sm="12" md="6">
              <h3 class="white--text">Device Location Address</h3>
              <v-row>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field outlined dense label="Site Id" v-model="item.site_id" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field outlined dense label="Site Name" v-model="item.site_name" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field outlined dense label="Street Address" v-model="item.site_address" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field outlined dense label="City/Town" v-model="item.site_city" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field outlined dense label="State" v-model="item.site_state" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field outlined dense label="Zip/Postcode" v-model="item.site_zip" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field outlined dense label="Country" v-model="item.site_country" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field outlined dense label="Latitude" v-model="item.site_lat" required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field outlined dense label="Longitude" v-model="item.site_lon" required :rules="[rules.required]" ></v-text-field>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" :disabled="!valid" @click="handleSave"> Save </v-btn>
      </v-card-actions>
    </v-card>

    <!--Confirm dialog-->
    <v-dialog v-model="confirmDialog" persistent max-width="290" >
      <v-card>
        <v-card-title class="headline">
          Are you sure?
        </v-card-title>
        <v-card-text>Device Id = <b>{{uuid}}</b></v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="confirmDialog = false" >No</v-btn>
          <v-btn color="blue darken-1" text @click="onYes()">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import dialog-->
    <v-dialog v-model="importDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Import Device</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport" ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="saveImport" :disabled="!dataImport">Continue</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
import {copyToClipboard, isJsonString} from "../utils/utils";
  import axios from "../axios";

  export default {
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      valid: false,
      confirmDialog: false,
      importDialog: false,
      uuid: '',
      dataImport: '',
      item: {}
    }),
    async mounted() {
      this.getData();
    },
    methods:{
      clickJsonExport(){
        const data = _.cloneDeep(this.item);
        data.device_id = null;
        const status = copyToClipboard(JSON.stringify(data));
        const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
        const type = status ? `success` : `error`;
        return this.$toasted.show(msg, {type: type});
      },
      async handleSave() {
        if(this.$refs.form.validate()){
          axios.put(`rubix-service/plat`, this.item).then(({data}) => {
            this.$toasted.show("success");
            this.getData();
          });
        }
      },
      closeDialogImport() {
        this.importDialog = false;
        this.$nextTick(() => {
          this.dataImport = "";
        });
      },
      async saveImport() {
        if(!isJsonString(this.dataImport)){
          return this.$toasted.show("incorrect data type!", {type: 'error'});
        }
        this.item = JSON.parse(this.dataImport);
        this.closeDialogImport();
      },
      getData() {
        axios.get(`rubix-service/plat`).then((data) => {
          this.item = data;
        });
      },
      async generateUuid() {
        this.uuid = await axios.get('generate-uuid');
        this.confirmDialog = true;
      },
      onYes(){
        this.item.device_id = this.uuid;
        this.confirmDialog = false;
      },
    },
  };
</script>
