<template>
    <v-card>
        <v-card-title>
            <span>Update Password</span>
        </v-card-title>
        <v-card-text>
            <v-container>
                <v-form v-model="valid" ref="form" lazy-validation>
                    <v-row>
                        <v-col cols="24">
                            <v-text-field v-model="currentPassword" type="password" @input="onChangePassword" label="Current Password" required :rules="[rules.required]"></v-text-field>
                        </v-col>                    
                    </v-row>
                    <v-row>
                         <v-col cols="24">
                            <v-text-field v-model="newPassword" type="password" @input="onChangePassword" label="New Password" required :rules="[rules.required]"></v-text-field>
                        </v-col>
                        
                    </v-row>
                     <v-row>
                        <span v-show="!this.newPassword.match('(?=.*?[A-Z])')" class="noti-regex">At least 1 Uppercase</span>             
                    </v-row>
                    <v-row>
                        <span v-show="!this.newPassword.match('(?=.*?[0-9])')" class="noti-regex">At least 1 Number </span>
                    </v-row>
                    <v-row>
                        <span v-show="this.newPassword.length<8" class="noti-regex">At least 8 charaters</span>
                    </v-row>
                    <v-row>
                        <v-col cols="24">
                            <v-text-field v-model="retypePassword" type="password" @input="onChangePassword" label="Retype Password" required :rules="[rules.required]"></v-text-field>
                        </v-col> 
                    </v-row>
                </v-form>
                   
            </v-container>
        </v-card-text>
         <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
          <v-btn color="blue darken-1" :disabled="disable" text @click="onSave(formData)"> Save </v-btn>
        </v-card-actions>
    </v-card>
</template>
<script>
export default {
 
  props: ['onSave','onCancel','item'],
  data:()=> ({
      rules: { required: (value) => !!value || "Required." },
      valid: false,
      newPassword:'',
      retypePassword:'',
      currentPassword:'',
      disable:true,
  }),
  methods: {
      onChangePassword(){
        this.disable=true;
        if(this.newPassword.match('(?=.*?[A-Z])(?=.*?[0-9]).{8,}') && this.retypePassword===this.newPassword){
            this.disable=false;
        }
      }
  },
  computed:{
      formData(){
          const data={
              currentPassword:this.currentPassword,
              newPassword:this.newPassword,
              retypePassword:this.retypePassword
          }
          return data;
      }
  }
    
}
</script>
<style scoped>
    .noti-regex{
        margin-left: 10px;
    }
</style>