<template>
  <v-card>
    <v-card-text>
      <v-container>
        <v-form v-model="valid" ref="form" lazy-validation>
          <v-row>
            <v-col cols="12" sm="6" md="4">
              <v-text-field v-model="formData.name" label="Name" required :rules="[rules.required]"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="4">
              <v-text-field v-model="formData.host" label="IP" required :rules="[rules.required]"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="4">
              <v-text-field v-model="formData.port" label="Port" required :rules="[rules.required]"></v-text-field>
            </v-col>
            <v-row v-if="setting.isGlobal" cols="12" sm="12" md="12" class="p-relative" style="margin:0">
              <v-col cols="6">
                <v-switch @change="changeUseGlobalIp"  v-model="useGlobalIp" label="Use Global Ip"></v-switch>
              </v-col>
            </v-row>
          </v-row>
        </v-form>
      </v-container>
    </v-card-text>

    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="blue darken-1" :disabled="!valid" text @click="onSave(formData)"> Save </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>

  import axios from "../axios";

  export default {
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      valid: false,
      useGlobalIp: false,
    }),
    computed: {
      formData () {
        const { mqttConfig } = this.$store.state;
        this.useGlobalIp = mqttConfig.host === this.setting.globalIp;
        return mqttConfig;
      },
      setting(){
        return this.$store.state.setting;
      }
    },
    methods: {
      changeUseGlobalIp(){
        if (this.useGlobalIp) {
          this.formData.host = this.setting.globalIp;
        }
      },
      onSave() {
        axios.put('mqtt-config', this.formData).then(r => {
          this.$toasted.show('Success');
          this.$store.commit('changeMqttConfig', this.formData);
        });
      },

    },

  };
</script>
<style>
  .p-relative{
    position: relative;
  }
</style>
