
<template>
<div style="padding:20px">
  <v-row>
    <v-col class="text-left" xl="3" lg="3" sm="12">
      <v-btn x-small color="primary" @click="clickImport" >
        Import&nbsp;<v-icon small>mdi-import</v-icon>
      </v-btn>
      <v-divider inset vertical></v-divider>
      <v-btn x-small @click="clickJsonExport" color="#009688" class="white--text">
        Export&nbsp;<v-icon small>mdi-export</v-icon>
      </v-btn>
    </v-col>
  </v-row>
  <v-row>
    <v-col sm="12" xs="12" xl="3" lg="3">
      <v-checkbox label="Services Manager" v-model="setting.services" @change="save" />
    </v-col>
    <v-col sm="12" xs="12" xl="3" lg="3">
        <v-checkbox label="File Manager" v-model="setting.fileManager" @change="save" />
    </v-col>
    <v-col sm="12" xs="12" xl="3" lg="3">
      <v-checkbox label="LoRa Network" v-model="setting.lora" @change="save" />
    </v-col>
    <v-col sm="12" xs="12" xl="3" lg="3">
      <v-checkbox label="BACnet Server" v-model="setting.bacnetServer" @change="save" />
    </v-col>
    <v-col sm="12" xs="12" xl="3" lg="3">
      <v-checkbox label="Mobus" v-model="setting.modbus" @change="save" />
    </v-col>
    <v-col sm="12" xs="12" xl="3" lg="3">
      <v-checkbox label="Mqtt" v-model="setting.mqtt" @change="save" />
    </v-col>
    <v-col sm="12" xs="12" xl="3" lg="3">
      <v-checkbox label="Cloud Networks" v-model="setting.cloudNetworks" @change="save" />
    </v-col>
    <v-col sm="12" xs="12" xl="3" lg="3">
      <v-checkbox label="Logging" v-model="setting.logging" @change="save" />
    </v-col>
    <v-col sm="12" xs="12" xl="3" lg="3">
      <v-checkbox label="Api Client" v-model="setting.apiClient" @change="save" />
    </v-col>
    <v-col sm="12" xs="12" xl="3" lg="3">
      <v-checkbox label="App Manager" v-model="setting.appManager" @change="save" />
    </v-col>
    <v-col sm="12" xs="12" xl="3" lg="3">
      <v-checkbox label="Rubix Apps" v-model="setting.rubixApps" @change="save" />
    </v-col>
  </v-row>

  <!--Import dialog -->
  <v-dialog v-model="importDialog" max-width="500px">
    <v-card>
      <v-card-title>
        <span class="headline">Import</span>
      </v-card-title>

      <v-card-text>
        <v-alert
          class="text-left"
          dense
          colored-border
          type="error"
          elevation="2"
          v-show="isValidData"
        >Incorrect Data Type!</v-alert>
        <v-container>
          <v-textarea
            outlined
            name="input-7-4"
            label="import data"
            v-model="dataImport"
            @input="onChangeImportData"
          ></v-textarea>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
        <v-btn color="blue darken-1" :disabled="disableSave" text v-on:click="saveImport">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>

</div>

</template>
<script>
import axios from "../axios";
import { copyToClipboard , isJsonString, isValidObject } from '../utils/utils';
const requireKeys = ["services","fileManager","lora","bacnetServer","modbus", 'logging', 'mqtt', 'apiClient', 'rubixApps'];
export default {
   data(){
     return{
      importDialog:false,
      dataImport:"",
      isValidData:false,
      disableSave:true,
     }
   },
   computed: {
    setting () {
      return this.$store.state.setting;
    },
    loading () {
      return this.$store.state.loading;
    }
  },
  methods:{
    closeDialogImport(){
      this.importDialog = false;
    },
    async save(){
      this.$store.commit('changeLoading', true);
      axios.put(`setting`, this.setting).then(result => {
        this.$store.commit('changeLoading', false);
        if (result === 'successful') {
          this.$store.commit('changeSetting', this.setting);
          this.$toasted.show('Success!')
        }
      });
    },
    async onChangeImportData(){
      this.isValidData =this.disableSave= !isJsonString(this.dataImport) || Array.isArray(JSON.parse(this.dataImport)) || !isValidObject(this.dataImport,requireKeys);
    },
    clickImport(){
      this.importDialog = true;
    },
    clickJsonExport(){
      const sideBar = {
        services: this.setting.services,
        fileManager: this.setting.fileManager,
        lora: this.setting.lora,
        bacnetServer: this.setting.bacnetServer,
        apiClient: this.setting.apiClient,
        modbus:this.setting.modbus,
        mqtt: this.setting.mqtt,
        logging: this.setting.logging,
        appManager: this.setting.appManager,

      };
      const status = copyToClipboard(JSON.stringify(sideBar));
      const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    async saveImport(){
      if(!isJsonString(this.dataImport)){
        return this.$toasted.show("incorrect data type!",{type:'error'});
      }
      const importData=JSON.parse(this.dataImport);
      this.$store.commit('changeLoading', true);
      const result = await axios.put(`setting`,importData);
      this.$store.commit('changeLoading', false);
      if (result === 'successful') {
        this.$store.commit('changeSetting',importData);
        this.importDialog = false;
        this.dataImport = '';
      }
    },
  }
}
</script>

