<template>
<div style="padding:20px">
  <v-row>
    <v-col class="text-left" xl="3" lg="3" sm="12">
        <v-btn x-small color="primary" @click="clickImport" >
          Import&nbsp;<v-icon small>mdi-import</v-icon>
        </v-btn>
        <v-divider inset vertical></v-divider>
        <v-btn x-small @click="clickJsonExport" color="#009688" class="white--text">
          Export&nbsp;<v-icon small>mdi-export</v-icon>
        </v-btn>
      </v-col>
  </v-row>
  <v-row>
    <v-col sm=4>
      <v-card outlined class="card-height">
        <v-card-title>
          General
          <v-row align="center" justify="end" >
            <v-avatar size="35">
              <img alt="logo" :src="logo">
            </v-avatar>
          </v-row>
        </v-card-title>
        <v-card-text>
          <!-- <v-row>
            <v-col class="p-relative" sm="5"> -->
              <!-- <v-text-field label="Device user" v-model="setting.deviceUser" :disabled="!enableDeviceUser"></v-text-field>
              <span v-show="!setting.deviceUser" class="required-noti">Required</span>
            </v-col>
            <v-col sm="2">
              <v-switch @change="onChangeEnable('enableDeviceUser', 'deviceUser')" v-model="enableDeviceUser"></v-switch>
            </v-col>
          </v-row> -->
          <v-row>
            <!-- <v-col sm="5" class="p-relative">
              <v-text-field label="Lib Path" v-model="setting.libPath" :disabled="!enableLibPath" ></v-text-field>
              <span v-show="!setting.libPath" class="required-noti">Required</span>
            </v-col>
            <v-col sm="2">
              <v-switch @change="onChangeEnable('enableLibPath', 'libPath')" v-model="enableLibPath"></v-switch>
            </v-col> -->
            <v-col sm="10" class="p-relative">
              <v-text-field label="Global Ip" v-model="setting.globalIp" :disabled="!setting.isGlobal" :rules="globalIpRule"></v-text-field>
            </v-col>
            <v-col sm="2">
              <v-switch v-model="setting.isGlobal"></v-switch>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col sm=4>
      <v-card outlined class="card-height">
        <v-card-title>
          Rubix Services
          <v-row align="center" justify="end" >
            <v-avatar size="35">
              <img alt="logo" :src="logo">
            </v-avatar>
          </v-row>
        </v-card-title>
        <v-card-text>
          <v-row>
            <v-col class="p-relative">
              <v-text-field :disabled="setting.isGlobal" label="Device IP" v-model="setting.rubixServiceIp" ></v-text-field>
              <span v-show="!setting.isGlobal && !setting.rubixServiceIp" class="required-noti">Required</span>
            </v-col>
          </v-row>

          <v-row>
            <v-col sm="10" class="p-relative">
              <v-text-field label="Port" v-model="setting.rubixServicePort"  :disabled="!enablerubixServicePort"></v-text-field>
              <span v-show="!setting.rubixServicePort" class="required-noti">Required</span>
            </v-col>
            <v-col sm="2">
              <v-switch @change="onChangeEnable('enablerubixServicePort', 'rubixServicePort')" v-model="enablerubixServicePort"></v-switch>
            </v-col>
          </v-row>
          <v-text-field style="visibility: hidden;"></v-text-field>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>

  <v-card-actions style="padding:0">
    <v-spacer></v-spacer>
    <v-btn :loading="loading" color="blue darken-2" @click="save">Save</v-btn>
  </v-card-actions>
   <!--Import dialog -->
      <v-dialog v-model="importDialog" max-width="500px">
        <v-card>
          <v-card-title>
            <span class="headline">Import</span>
          </v-card-title>

          <v-card-text>
            <v-alert
              class="text-left"
              dense
              colored-border
              type="error"
              elevation="2"
              v-show="isValidData"
            >Incorrect Data Type!</v-alert>
            <v-container>
              <v-textarea
                outlined
                name="input-7-4"
                label="import data"
                v-model="dataImport"
                @input="onChangeImportData"
              ></v-textarea>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
            <v-btn color="blue darken-1" :disabled="disableSave" text v-on:click="saveImport">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
</div>
</template>

<script>
import logo from "../serviceicons/nube-frog-green.png";
import axios from "../axios";
import { copyToClipboard , isJsonString, isValidObject } from '../utils/utils';

const requireKeys = [
  "deviceUser",'libPath', 'isGlobal', "globalIp",
  "rubixServiceIp", "rubixServicePort"
];

export default {
  data(){
    return{
      logo,
      globalIpRule:[],
      importDialog:false,
      dataImport:"",
      isValidData:false,
      disableSave:true,
      enableDeviceUser:false,
      enableLibPath:false,
      enablerubixServicePort:false,
      defaultSetting: {},
      customSetting: {...this.$store.state.setting }
    }
  },
  watch: {
    'setting.deviceUser': function (val) {
      if (val === this.defaultSetting.deviceUser) {
        return;
      }
      this.customSetting = {...this.customSetting, deviceUser: val};
    },
    'setting.libPath': function (val) {
      if (val === this.defaultSetting.libPath) {
        return;
      }
      this.customSetting = {...this.customSetting, libPath: val};
    },
    'setting.rubixServicePort': function (val) {
      if (val === this.defaultSetting.rubixServicePort) {
        return;
      }
      this.customSetting = {...this.customSetting, rubixServicePort: val};
    },
  },
  computed: {
    setting() {
      return this.$store.state.setting;
    },
    loading() {
      return this.$store.state.loading;
    },
  },
  mounted() {
    this.getDefaultSetting();
  },
  methods: {
    onChangeEnable(enableKey, customKey) {
      if (this[enableKey]) {
        this.setting[customKey] = this.customSetting[customKey];
      } else {
        this.setting[customKey] = this.defaultSetting[customKey];
      }
    },
    async getDefaultSetting() {
      axios.get('setting/get-default').then(r => {
        this.defaultSetting = r;
        this.enableDeviceUser = r.deviceUser !== this.setting.deviceUser;
        this.enableLibPath = r.libPath !== this.setting.libPath;
        this.enablerubixServicePort = r.rubixServicePort !== this.setting.rubixServicePort;
      });
    },
    async save(){
      if(this.setting.isGlobal && !this.setting.globalIp){
        this.globalIpRule=[ v => !!v || 'Please Input Global IP'];
        return;
      }
      this.$store.commit('changeLoading', true);
      axios.put(`setting`, this.setting).then(result => {
        this.$store.commit('changeLoading', false);
        if (result === 'successful') {
          this.$store.commit('changeSetting', this.setting);
          this.$toasted.show('Success!')
          this.$store.commit('changePingData',{});
        }
      });
    },
    closeDialogImport(){
      this.importDialog = false;
    },
    clickJsonExport(){
      const serviceSetting ={
        deviceUser:this.setting.deviceUser,
        libPath:this.setting.libPath,
        isGlobal:this.setting.isGlobal,
        globalIp:this.setting.globalIp,
        rubixServiceIp:this.setting.rubixServiceIp,
        rubixServicePort:this.setting.rubixServicePort,
       };
      const status = copyToClipboard(JSON.stringify(serviceSetting));
      const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    clickImport(){
      this.importDialog = true;
    },
    async onChangeImportData(){
      this.isValidData = this.disableSave= !isJsonString(this.dataImport) || Array.isArray(JSON.parse(this.dataImport)) || !isValidObject(this.dataImport,requireKeys);
    },
    async saveImport(){
      if(!isJsonString(this.dataImport)){
        return this.$toasted.show("incorrect data type!",{type:'error'});
      }
      const importData=JSON.parse(this.dataImport);
      this.$store.commit('changeLoading', true);
      const result = await axios.put(`setting`,importData);
      this.$store.commit('changeLoading', false);
      if (result === 'successful') {
        this.$store.commit('changeSetting',importData);
        this.importDialog = false;
        this.dataImport = '';
      }
    }
  },
};
</script>

<style>
.card-height{
    height: 280px;
  }
  .p-relative{
    position: relative;
  }
  .required-noti {
    position: absolute;
    padding-left: 10px;
    bottom: 10px;
    left: 0;
    color: red;
    font-size: smaller;
  }
</style>
