<template>
  <div>
    <div class="top-bar-tools">
      <v-btn @click="dialogDelete = true, isDeleteAll = false" :disabled="selected.length === 0" x-small color="error" class="white--text">
          Delete <v-icon x-small right dark > mdi-delete </v-icon>
      </v-btn>&nbsp;
      <v-btn @click="dialogDelete = true, isDeleteAll = true" x-small color="error" class="white--text"> Delete all </v-btn>
    </div>

    <v-data-table
      v-model="selected"
      :headers="headers"
      :items="logs"
      class="elevation-1"
      item-key="time"
      show-select
      hide-default-footer
      disable-sort>
    </v-data-table>

    <!-- Delete dialog -->
    <v-dialog v-model="dialogDelete" persistent max-width="290">
      <v-card>
        <v-card-title> Confirm Delete</v-card-title>
        <v-card-text>
          <v-container>
            <v-row>Are you sure?</v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogDelete = false"> No </v-btn>
          <v-btn color="red darken-1" text @click="confirmDelete"> Yes </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>

export default {
  data:()=>({
    logs: [],
    selected: [],
    headers: [
      { text: "Message", value: "message", width: '50%'},
      { text: "Description", value: "description"},
    ],
    dialogDelete: false,
    isDeleteAll: false,
  }),

  methods: {
    deleteAll() {
      localStorage.removeItem("logs");
      this.logs = [];
    },
    deleteSelected() {
      this.logs = this.logs.filter(log => !this.selected.includes(log));
      localStorage.setItem('logs', JSON.stringify(this.logs));
    },
    confirmDelete(){
      this.isDeleteAll ? this.deleteAll() : this.deleteSelected();
      this.dialogDelete = false;
    },
  },
  mounted() {
    const logs = JSON.parse(localStorage.getItem('logs')) || [];
    this.logs = logs;
  }
};
</script>
<style scoped>
.top-bar-tools {
  margin-bottom: 10px;
  text-align: left;
}
</style>
