    //  });
    //   return data;
    // },
    convertMQTT(mqtt){
      _.forEach(mqtt.subscriptions, (subscription,index)=>{
        _.forIn(subscription, (subscription_value, subscription_key) => {
          const newKey = `subscription[${index}].${subscription_key}`;
          mqtt = {...mqtt, [newKey] : subscription_value}
        });
      });
      delete mqtt.subscriptions;
      return mqtt;
    },
    convertLoraDevices(loraDevices){
      const data = _.cloneDeep(loraDevices).map(device=>{
        // _.forIn(device, (value, key) => {
        //   if (_.isArray(value)) { //get points
        //     _.forEach(value, (item, index)=>{
