        //           const pointKey = `point[${indexP}].${point_key}`;
        //           device = {...device, [pointKey] : point_value}
        //         });
        //       });
        //     delete device.points;
        //   }
        // });
        delete device.points;
        delete device.uuid;
        return device;
     });
      return data;
    },
    convertPoints(points){
      const data = points.map(item => {
        // const keys = Object.keys(item.point_store);
        // if (keys && keys.length > 0) {
        //   keys.forEach(key => {
        //     const newKey = `point_store.${key}`;
        //     item = { ...item, [newKey]: item.point_store[key] };
        //   });
        // }
        delete item.point_store;
        delete item.uuid;
        return item;
      });
      return data;
    },
    getHeaders(key){
      if(_.isArray(this.items[key])){
        if(this.items[key][0]){
          const headerA = Object.keys(this.items[key][0]).map(title=>{
            const data =  { text: title, value: title };
            return data;
          });
          this.headers = {...this.headers, [key]: headerA};
        }
      }else{
        const headerB = Object.keys(this.items[key]).map(title=>{
          const data =  { text: title, value: title };
          return data;
        });
        this.headers = {...this.headers, [key]: headerB};
      }
    },
    async onChangeImportData() {
      this.isValidData = this.disableSave =
        !isJsonString(this.dataImport) || Array.isArray(JSON.parse(this.dataImport));
    },
    async clickImport() {
      this.dialogImport = true;
      this.dataImport = "";
    },
    async closeDialogImport() {
      this.dialogImport = false;
    },
    async saveImport() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      const importData = JSON.parse(this.dataImport);

      //delete array or object without value
      Object.keys(importData).forEach(key=>{
        if( (_.isArray(importData[key]) && importData[key].length == 0) || (!_.isArray(importData[key]) && Object.keys(importData[key]).length == 0) ){
          delete importData[key]
        }
      });
      //end delete

      this.items = importData;
      this.dialogImport = false;
      this.dataImport = '';
      this.showImportData();
    },
    async clickJsonExport() {
      await axios.get('export').then(r => {
        copyToClipboard(JSON.stringify(r));
        this.$toasted.show(`Clipboard Copy Successful!`, { type: 'success' });
      })
      .catch(err => {
        this.$toasted.show(`Failure on copying on clipboard!`, { type: 'error' });
      });
    },
    checkType(type, value){
      if(type == "array"){
        return _.isArray(value);
      }
      if(type == "boolean"){
        return _.isBoolean(value);
      }
      if(type == "number"){
        return _.isNumber(value);
      }
    },
    saveEdit() {},
    cancel() {},
    open() {},
    close() {},
    async save() {
      const data = _.cloneDeep(this.items);
      _.forEach(Object.keys(data), key => {
        if(key == "mqtt"){
          data[key] = this.getSubcriptionArray(data[key]);
        }
        // if(key == "loraDevices"){
        //   data[key] = this.getPointsArray(data[key]);
        // }
        // if(key == "apiClient"){
        //   data[key] = this.getItemsArray(data[key]);
        // }
      });
      await axios.post('import', data).then(r => {
        this.items = {};
        this.tabs = [];
        this.$router.push({ path: 'app-manager' });
        this.dialogResponse = true;
        this.responses = r;
        // this.$toasted.show('Success', { type: 'success' });
      })
      .catch(err => {
        this.$toasted.show(`Import Fail!`, { type: 'error' });
      });
    },
    // getItemsArray(apiClients){
    //   const data = _.cloneDeep(apiClients).map(apiClient=>{
    //     let items = [];
    //     let indexs = [];
    //     let itemKeys = [];
    //     Object.keys(apiClient).forEach(key =>{
    //       let keys = key.split('.');
    //       let itemKey = "";
    //       let itemIndex = null;
    //       if(keys.length > 1){
    //         itemKey = keys[1];
    //         if(!itemKeys.includes(itemKey)){
    //           itemKeys.push(itemKey);
    //         }
    //         itemIndex = Number(keys[0].split("[")[1].slice(0, -1));
    //         if((itemIndex || itemIndex === 0) && !indexs.includes(itemIndex)){
    //           indexs.push(itemIndex);
    //         }
    //       }
    //     })
    //     _.forEach(indexs, i=>{
    //       let item = {};
    //       _.forEach(itemKeys,ik=>{
    //         item = {...item, [ik]: apiClient[`items[${i}].${ik}`]};
    //         delete apiClient[`items[${i}].${ik}`];
    //       });
    //       items.push(item);
    //     })
    //     apiClient.items = items;
    //     return apiClient;
    //   });
    //   return data
    // },
    // getPointsArray(devices){
    //   const data = _.cloneDeep(devices).map(device=>{
    //     let points = [];
    //     let indexs = [];
    //     let pointKeys = [];
    //     Object.keys(device).forEach(key =>{
    //       let keys = key.split('.');
    //       let pointKey = "";
    //       let pointIndex = null;
    //       if(keys.length > 1){
    //         pointKey = keys[1];
    //         if(!pointKeys.includes(pointKey)){
    //           pointKeys.push(pointKey);
    //         }
    //         pointIndex = Number(keys[0].split("[")[1].slice(0, -1));
    //         if((pointIndex || pointIndex === 0) && !indexs.includes(pointIndex)){
    //           indexs.push(pointIndex);
    //         }
    //       }
    //     })
    //     _.forEach(indexs, i=>{
    //       let point = {};
    //       _.forEach(pointKeys,pk=>{
    //         point = {...point, [pk]: device[`point[${i}].${pk}`]};
    //         delete device[`point[${i}].${pk}`];
    //       });
    //       points.push(point);
    //     })
    //     device.points = points;
    //     return device;
    //   });
    //   return data
    // },
    getSubcriptionArray(mqtt){
      let subscriptions = [];
      let indexs = [];
      let subscriptionKeys = [];
      Object.keys(mqtt).forEach(key =>{
        let keys = key.split('.');
        let subscriptionKey = "";
        let subscriptionIndex = null;
        if(keys.length > 1){
          subscriptionKey = keys[1];
          if(!subscriptionKeys.includes(subscriptionKey)){
            subscriptionKeys.push(subscriptionKey);
          }
          subscriptionIndex = Number(keys[0].split("[")[1].slice(0, -1));
          if((subscriptionIndex || subscriptionIndex === 0) && !indexs.includes(subscriptionIndex)){
            indexs.push(subscriptionIndex);
          }
        }
      })
      _.forEach(indexs, i=>{
        let subscription = {};
        _.forEach(subscriptionKeys,sk=>{
          subscription = {...subscription, [sk]: mqtt[`subscription[${i}].${sk}`]};
          delete mqtt[`subscription[${i}].${sk}`];
        });
        subscriptions.push(subscription);
      })
      mqtt.subscriptions = subscriptions;
      return mqtt;
    },
  },
};
</script>

