<template>
<div>
  <v-card>
    <v-card-text>
      <v-card-title>
        <v-btn @click="importDialog = true, dataImport = ''" x-small color="primary" class="white--text">
          Import<v-icon x-small right dark>mdi-import</v-icon>
        </v-btn>&nbsp;
        <v-btn @click="clickJsonExport" x-small color="#009688" class="white--text" >
          Export<v-icon x-small right dark > mdi-export </v-icon>
        </v-btn>
      </v-card-title>
      <v-container>
        <v-form v-model="valid" ref="form" lazy-validation>
          <v-row>
            <v-col>
              <v-text-field v-model="item.ip" label="Ip" required :rules="[rules.required]"></v-text-field>
            </v-col>
            <v-col>
              <v-text-field v-model="item.port" label="Port" required :rules="[rules.required]"></v-text-field>
            </v-col>
            <v-col>
              <v-text-field v-model="item.device_id" label="Device Id" required :rules="[rules.required]"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field disabled v-model="item.model_name" label="Model Name" required :rules="[rules.required]"></v-text-field>
            </v-col>
            <v-col>
              <v-text-field v-model="item.local_obj_name" label="Local Obj Name" required :rules="[rules.required]"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field disabled v-model="item.vendor_id" label="Vendor Id" required :rules="[rules.required]"></v-text-field>
            </v-col>
            <v-col>
              <v-text-field disabled v-model="item.vendor_name" label="Vendor Name" required :rules="[rules.required]"></v-text-field>
            </v-col>
          </v-row>
        </v-form>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="blue darken-1" :disabled="!valid" text @click="onSave(item)"> Save </v-btn>
    </v-card-actions>
  </v-card>
  <!--Import dialog -->
  <v-dialog v-model="importDialog" max-width="500px">
    <v-card>
      <v-card-title> Import </v-card-title>
      <v-card-text>
        <v-container>
          <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="darken-1" text @click="importDialog = false">Cancel</v-btn>
        <v-btn color="blue darken-1" text v-on:click="saveImport">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
  </div>
</template>

<script>

import axios from "../axios";
import { copyToClipboard , isJsonString, isValidObject } from '../utils/utils';

  export default {
    props: [],
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      valid: false,
      item: {},
      importDialog: false,
      dataImport: "",
    }),
    mounted() {
      this.getBacnetInfo();
    },
    methods: {
      async getBacnetInfo() {
        this.item = await axios.get(`bac-net/server-info`);
      },
      async onSave(data) {
        await axios.patch(`bac-net/server-info`, data)
        .then(r => {
          this.$toasted.show('Success');
          this.getBacnetInfo();
        }).catch(e => {
          this.$toasted.show(e.message, {type: 'error'});
        });
      },
      clickJsonExport(){
        const status = copyToClipboard(JSON.stringify(this.item));
        const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
        const type = status ? `success` : `error`;
        return this.$toasted.show(msg, {type: type});
      },
      async saveImport(){
        if(!isJsonString(this.dataImport)){
          return this.$toasted.show("incorrect data type!", {type:'error'});
        }
        const importData = JSON.parse(this.dataImport);
        this.onSave(importData);
      },
    },
  };
</script>
