<template>
  <v-data-table
    :headers="headers"
    :items="data"
    class="elevation-1"
    item-key="uuid"
    show-select
    disable-sort>
    <template v-slot:no-data> No data </template>
  </v-data-table>
</template>

<script>

import axios from "../axios";

export default {
  name: "networking",
  data:()=>({
    headers: [],
    data: []
  }),
  mounted(){
    this.getData();
  },
  methods: {
    async getData() {
      const items = await axios.get('rubix-service/networking');
      const data = [];
      const headers = [{text: '', value: 'name'}];
      for (const key in items) {
        data.push({...items[key], name: key});
      }
      const fields = items[Object.keys(items)[0]];
      for (const key in fields) {
        headers.push({text: key, value: key})
      }
      this.headers = headers;
      this.data = data;
    }
  }
};
</script>
