<template>
   <div>
     <v-row>
      <v-col class="text-left" xl="3" lg="3" sm="12">
        <v-btn x-small color="primary" @click="clickImport" >
          Import&nbsp;<v-icon small>mdi-import</v-icon>
        </v-btn>
        <v-divider inset vertical></v-divider>
        <v-btn x-small @click="clickJsonExport" color="#009688" class="white--text">
          Export&nbsp;<v-icon small> mdi-export </v-icon>
        </v-btn>
      </v-col>
      <v-col xl="6" lg="6" sm="12">
        <h1>Settings</h1>
      </v-col>
    </v-row>
    <v-tabs fixed-tabs v-model="tab">
      <v-tab :key="0">
        SIDEBAR SETTING
      </v-tab>
      <v-tab :key="1">
        SERVICE DATA
      </v-tab>
      <v-tab :key="2">
        SHOW/HIDE SERVICE
      </v-tab>
      <v-tab :key="3">
        APPS
      </v-tab>
      <v-tab :key="4">
        SERVICES
      </v-tab>
      <v-tab :key="5">
        MQTT Config
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <sidebar-setting></sidebar-setting>
      </v-tab-item>
      <v-tab-item :key="1">
        <service-data></service-data>
      </v-tab-item>
      <v-tab-item :key="2">
        <show-hide-service></show-hide-service>
      </v-tab-item>
      <v-tab-item :key="3">
        <iframe-setting></iframe-setting>
      </v-tab-item>
      <v-tab-item :key="4">
        <service-setting></service-setting>
      </v-tab-item>
      <v-tab-item :key="5">
        <mqtt-form></mqtt-form>
      </v-tab-item>
    </v-tabs-items>
    <!--Import dialog -->
    <v-dialog v-model="dialogImport" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Import</span>
        </v-card-title>

        <v-card-text>
          <v-alert
            class="text-left"
            dense
            colored-border
            type="error"
            elevation="2"
            v-show="isValidData"
          >Incorrect Data Type!</v-alert>
          <v-container>
            <v-textarea
              outlined
              name="input-7-4"
              label="import data"
              v-model="dataImport"
              @input="onChangeImportData"
            ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
          <v-btn color="blue darken-1" :disabled="disableSave" text v-on:click="saveImport">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import SidebarSetting from './SidebarSetting';
import MqttForm from './MqttForm';
import ServiceData from './ServiceData';
import ShowHideService from './ShowHideService';
import ServiceSetting from './ServiceSetting';
import {isJsonString,copyToClipboard,deleteIdKey} from '../utils/utils';
import axios from "../axios";
import IframeSetting from './IframeSetting';

export default {
  components:{
    SidebarSetting,
    ServiceData,
    ShowHideService,
    ServiceSetting,
    IframeSetting,
    MqttForm
  },
  data:()=>({
    tab: 0,
    tabs: ['sidebar', 'service-data', 'service-page', 'apps', 'services', 'mqtt-config'],
    dialogImport:false,
    dataImport:"",
    isValidData:false,
    disableSave:true,
    iframeArr:[],
    serviceArr:[],
  }),
  computed: {
    setting () {
      return this.$store.state.setting;
    },
    loading () {
      return this.$store.state.loading;
    },
    iframes(){
      return this.$store.state.iframes;
    },
    services(){
      return this.$store.state.services;
    },
    mqttConfig(){
      return this.$store.state.mqttConfig;
    },
  },
  watch: {
    '$store.state.iframes':function(val){
      this.iframeArr = val;
    },
    '$store.state.services':function(val){
      this.serviceArr = val;
    },
    '$route.query.tab':function(val){
      this.tab = this.tabs.indexOf(val);
    },
    tab: function (val, oldVal) {
      const { tab } = this.$route.query;
      if (tab !== this.tabs[val]) {
        const tabKey = this.tabs[val];
        this.$router.push({ path: 'setting', query: { tab: tabKey } });
      }
    },
  },
  mounted(){
    const { tab: tabKey } = this.$route.query;
    this.tab = this.tabs.indexOf(tabKey);
    this.iframeArr = this.iframes;
    this.serviceArr = this.services;
  },
  methods:{
    async onChangeImportData(){
      this.isValidData= this.disableSave= !isJsonString(this.dataImport) || Array.isArray(JSON.parse(this.dataImport));
    },
    async clickImport(){
      this.dialogImport=true;
    },
    async closeDialogImport(){
        this.dialogImport=false;
    },
    isValidImportData(arrData,settingData){
      let result='through';
      for(let i=0;i<arrData.length;i++){
        if(!settingData.includes(arrData[i])){
          result='notFound';
          break;
        }
      }
      return result;
    },
    async saveImport(){
      if(!isJsonString(this.dataImport)){
        return this.$toasted.show("incorrect data type!",{type:'error'});
      }
      const importData = JSON.parse(this.dataImport);
      const promises = [];
      for (const key in importData) {
        const data = importData[key];
        switch (key) {
          case 'settings':
            promises.push(axios.put(`setting`, data));
            break;
          case 'iframes':
            for(let i = 0 ; i < data.length ;i++ ){
              promises.push(axios.put('iframe', data[i]))
            }
            break;
          case 'services':
            for(let i = 0; i < data.length; i++){
              promises.push(axios.put('service', data[i]))
            }
            break;
          case 'mqttConfig':
            promises.push(axios.put('mqtt-config', data))
            break;
        }
      }
      this.$store.commit('changeLoading', true);
      await Promise.all(promises);
      this.$store.commit('changeLoading', false);
      
      if (importData.mqttConfig) {
        this.$store.commit('changeMqttConfig', importData.mqttConfig);
      }

      if (importData.settings) {
        this.$store.commit('changeSetting', importData.settings);
      }

      if (importData.iframes) {
        const iframes = await axios.get('iframe');
        this.$store.commit('changeIframes', iframes);
      }

      if (importData.services) {
        const services = await axios.get('service');
        this.$store.commit('changeServices', services);
      }

      this.$toasted.show('Success', { type: "success" } );
      this.dialogImport=false;
      this.dataImport='';
    },
    clickJsonExport(){
      const exportData= Object.assign({}, {
        settings : deleteIdKey(this.setting),
        iframes : this.iframeArr,
        services : this.serviceArr,
        mqttConfig: this.mmqttConfig
      })
      const status = copyToClipboard(JSON.stringify(exportData));
      const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    }
  }
}
</script>

