<template>
  <v-card>
        <v-card-title>
          <span class="headline">Add</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-expansion-panels v-model="expansionData">
                <v-expansion-panel value="1" class="mb-1">
                  <v-expansion-panel-header>POINT INFOR</v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-row>
                      <v-col cols="12" sm="6" md="4">
                        <v-select v-model="formData.object_type" label="Object Type" :items="['analogOutput']" required :rules="[rules.required]"></v-select>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="formData.object_name" label="Object Name" required :rules="[rules.required]"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field type="number" v-model="formData.address" label="Address" required :rules="[rules.required]"></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="6" md="8">
                        <v-autocomplete v-model="formData.units" :items="units" :label="'Units'" persistent-hint required :rules="[rules.required]" >
                        </v-autocomplete>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="formData.description" label="Description" required :rules="[rules.required]"></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="formData.relinquish_default" label="Relinquish Default" type="number" required :rules="[rules.required]"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-select
                          v-model="formData.event_state"
                          label="Event State"
                          :items="['normal', 'fault', 'offnormal', 'highLimit', 'lowLimit', 'lifeSafetyAlarm']"
                          required :rules="[rules.required]"></v-select>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-checkbox v-model="formData.enable" label="Enable" required></v-checkbox>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-checkbox v-model="formData.fault" label="Fault" required></v-checkbox>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="formData.data_round" label="Data Round" type="number"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="formData.data_offset" label="Data Offset" type="number" ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="formData.value_operation" label="Data Operation" type="string" ></v-text-field>
                      </v-col>
