        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
          <v-btn color="blue darken-1" :disabled="!valid" text @click="handleSave"> Save </v-btn>
        </v-card-actions>
      </v-card>
</template>

<script>
  import { units } from "../constants/appConstant";

  export default {
    props: ['onSave', 'onCancel', 'item'],
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      expansionData: 0,
      valid: false,
      units
    }),
    computed: {
      formData () {
        // this.item.enable = this.item.enable ? this.item.enable : false;
        // this.item.fault = this.item.fault ? this.item.fault : false;
        return this.item ? { ...this.item } : { priority_array_write: {} };
      },
    },
    methods:{
      handleSave(){
        if(this.$refs.form.validate()){
          this.onSave(this.formData);
        }
      }
    },
  };
</script>
