// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import vuetify from './plugins/vuetify';
import Toasted from 'vue-toasted';
import moment from 'moment';
import Vuex from 'vuex';
import axios from 'axios';

window.EventBus = new Vue();

import _axios from './axios';
import * as rubxiAppActions from './actions/rubixAppActions';

_axios.get('setting').then(setting => {
  Vue.prototype.$http = axios;
  Vue.config.productionTip = false;

  Vue.use(Vuex);
  const store = new Vuex.Store({
    state: {
      setting,
      pingData: {},
      iframes: [],
      services: [],
      mqttConfig: {},
      loading: false,
      appDetails: [],
      installedServices: [],
      systemDefaultApps: [],
    },
    mutations: {
      changeSetting(state, payload) {
        state.setting = { ...state.setting, ...payload };
      },
      changeLoading(state, payload) {
        state.loading = payload;
      },
      changePingData(state, payload) {
        state.pingData = payload;
      },
      changeIframes(state, payload) {
        state.iframes = payload;
      },
      changeServices(state, payload) {
        state.services = payload;
      },
      changeAppDetails(state, payload) {
        state.appDetails = payload;
      },
      changeMqttConfig(state, payload) {
        state.mqttConfig = { ...state.mqttConfig, ...payload };
      },
      changeInstalledServices(state, payload) {
        state.installedServices = payload;
      },
      [rubxiAppActions.UPDATE_SYSTEM_DEFAULT_APPS](state, payload) {
        state.systemDefaultApps = payload;
      }
    },
  });
  Vue.use(Toasted, { duration: 5000, keepOnHover: true, position: 'bottom-right', type: 'success' });

  router.beforeEach((to, from, next) => {
    const { setting } = store.state;
    if (to.matched.some(record => record.meta.auth)) {
      if (!localStorage.getItem('token')) {
        next({
          path: '/login',
          params: { nextUrl: to.fullPath },
        });
      } else if (to.meta.settingKey && !setting[to.meta.settingKey]) {
        next({ path: '/exception-403' });
      } else {
        next();
      }
    } else {
      next();
    }
  });

  /* eslint-disable no-new */
  new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>',
    vuetify,
    store,
  });
  Vue.filter('formatDate', function(value) {
    if (value) {
      return moment(String(value)).format('MM/DD/YYYY hh:mm');
    }
  });
});
