<template>
  <v-card>
      <v-card-title>
        <span class="headline">Service</span>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-form v-model="valid" ref="form" lazy-validation>
            <v-row>
              <v-col cols="12" >
                <v-text-field v-model="item.label" label="Service Label" required :rules="[rules.required]"></v-text-field>
              </v-col>
              <v-col cols="8" >
                <v-text-field v-model="item.name" label="Systemd Service Name" required :rules="[rules.required]"></v-text-field>
              </v-col>
              <v-col cols="4" >
                <v-checkbox label="Show?" v-model="item.show" />
              </v-col>
              <v-col cols="5">
                <v-checkbox label="Internal Service?" v-model="item.isInternal" />
              </v-col>
              <v-col cols="7">
                <v-combobox
                  v-on:change="changeIcon"
                  :loading="loading"
                  :items="icons"
                  :search-input.sync="search"
                  label="Select Icon"
                  item-text="name"
                  item-value="name"
                  :value="item.icon"
                  :rules="[rules.required]"
                >
                  <template slot="item" slot-scope="{item}">
                    <v-icon dark> {{ `mdi-${item.name}` }} </v-icon> <v-spacer></v-spacer>{{item.name}}
                  </template>
                </v-combobox>
              </v-col>
            </v-row>
            <v-row v-if="item.isInternal">
              <v-col cols="12" >
                <v-text-field v-model="item.releaseUrl" label="Release Url" required :rules="[rules.required]"></v-text-field>
              </v-col>
              <v-col cols="6" >
                <v-text-field v-model="item.fromVersion" label="From Version"></v-text-field>
              </v-col>
              <v-col cols="12" >
                <v-checkbox label="On install stop running service ?" v-model="item.stopRunningService" />
              </v-col>
            </v-row>
          </v-form>
        </v-container>
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
        <v-btn color="blue darken-1" :disabled="!valid" text @click="onSave(item)"> Save </v-btn>
      </v-card-actions>
    </v-card>
</template>

<script>

  import axios from "../axios";

  export default {
    props: ['onSave', 'onCancel', 'item'],
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      valid: false,
      loading: false,
      search: null,
      select: null,
      typingText:'',
      icons: [],
    }),
    watch: {
      search (val) {
        val && val !== this.item.icon && this.getIcons(val)
      }
    },
    methods: {
      changeIcon({name}){
        if (name) {
          this.item.icon = name;
          return;
        }
        this.item.icon='';
      },
      getIcons (v) {
        this.loading = true;
        axios.get('icons', {params: {keyword: v}}).then(r => {
          this.icons = r;
          this.loading = false;
        });
      }
    }
  };
</script>
