<template>
  <v-card>
    <v-card-title>
      <span class="headline">Import</span>
    </v-card-title>
    <v-card-text>
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-textarea outlined name="input-7-4" label="Import data" v-model="dataImport" required :rules="[rules.required, () => checkIsJsonString(dataImport) || 'Incorrect Data Type!']"></v-textarea>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="darken-1" text @click="closeDialogImport(), (dataImport = '')">Cancel</v-btn>
      <v-btn color="blue darken-1" text v-on:click="onSave(dataImport)" :disabled="!valid" >Save</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import axios from '../axios';
import { isJsonString } from '../utils/utils';

export default {
  props: ['closeDialogImport', 'saveImport'],
  data: () => ({
    rules: { required: value => !!value || 'Required.' },
    dataImport: '',
    valid: false,
  }),
  mounted() {},
  methods: {
    checkIsJsonString(data){
      if(data){
        return isJsonString(data);
      }
    },
    onSave(dataImport) {
      if (this.$refs.form.validate()) {
        this.saveImport(dataImport);
        this.dataImport = '';
      }
    },
  },
};
</script>
