<template>
  <div>
    <div class="text-left overflow-auto">
      <v-switch label="Enable?" class="enable-switch" @change="() => save(item)" v-model="item.enable"></v-switch>
    </div>
    <v-row>
      <v-col cols="9">
        <v-form v-model="valid" ref="form" lazy-validation>
          <v-text-field
            @input="onChangeUrl"
            required
            :rules="urlRules"
            v-model="item.url"
            placeholder="Enter your request Url"
            @keyup.enter.native="send(item)"
          >
            <v-select
              class="slb-method"
              slot="prepend"
              v-model="item.method"
              :items="['Get', 'Post', 'Put', 'Patch', 'Delete']"
            ></v-select>
          </v-text-field>
        </v-form>
      </v-col>
      <v-col cols="3" class="tool-box">
        <v-btn small :disabled="!valid" @click="send(item)" color="primary"> Send </v-btn> &nbsp;
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-tabs v-model="tab">
          <v-tab>Params</v-tab>
          <v-tab>Headers</v-tab>
          <v-tab>Body</v-tab>
          <v-tab>Delay</v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="'params'">
            <key-value-form :items="item.params"></key-value-form>
          </v-tab-item>
          <v-tab-item :key="'headers'">
            <key-value-form :type="'header'" :items="item.headers"></key-value-form>
          </v-tab-item>
          <v-tab-item :key="'body'">
            <key-value-form :items="item.body"></key-value-form>
          </v-tab-item>
          <v-tab-item :key="'delay'">
            <v-text-field type="number" v-model="item.delay" label="Delay Value" ></v-text-field>
          </v-tab-item>
        </v-tabs-items>
      </v-col>
      <v-col style="background: #000">
        <vue-json-pretty
          :itemHeight="1000"
          :virtual="true"
          style="text-align: left"
          :data="resp || 'Hit Send to get a response'"
        >
        </vue-json-pretty>
      </v-col>
    </v-row>
  </div>
</template>

<script>
  import VueJsonPretty from 'vue-json-pretty';
  import 'vue-json-pretty/lib/styles.css';
  import KeyValueForm from './KeyValueForm.vue';
  import qs from 'query-string';
  import { copyToClipboard, isJsonString } from '../utils/utils';

  export default {
    props: ['item', 'save', 'remove', 'send', 'resp'],
    data: () => ({
      tab: '',
      valid: false,
      tabDialog: false,
    }),
    components: {
      VueJsonPretty,
      KeyValueForm,
    },
    computed: {
      urlRules() {
        return [value => !!value || 'Required.'];
      },
    },
    mounted() {
      this.$refs.form.validate();
    },
    methods: {
      onChangeUrl() {
        const valid = this.$refs.form.validate();
        if (valid) {
          const queryString = qs.extract(this.item.url);
          if (queryString) {
            const obj = qs.parse(queryString);
            const params = [];
            for (const key in obj) {
              if (key && obj[key]) {
                params.push({ key, value: obj[key], enable: true });
              }
            }
            this.item.params = params;
          }
        }
      },
    },
  };
</script>
<style>
  .v-input__prepend-outer {
    margin-top: 0 !important;
  }
  .slb-method {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
  .tool-box {
    display: flex;
    align-items: center;
    text-align: left;
  }
  .enable-switch {
    width: 42px;
    margin-top: 0 !important;
    float: left;
  }
</style>
