<template>
  <v-card-text>
    <page-title title="Host Info"></page-title>
    <div style="text-align: start">
      <v-row>
        <!-- System disc usage -->
        <v-col xl="4" lg="4" sm="12" md="6" xs="24">
          <v-card elevation="2" outlined tile class="card-height">
            <v-card-title>
              <h5>SYSTEM DISC USAGE</h5>
              <v-row align="center" justify="end">
                <v-avatar size="35">
                  <img alt="logo" :src="logo" />
                </v-avatar>
              </v-row>
            </v-card-title>
            <v-card-text class="card-text-height">
              <div v-for="(key, idx) in objectKey(freeDisc)" :key="idx">
                {{ toUpper(key) }}:&nbsp;<span class="value-style">{{ freeDisc[key] }}</span>
              </div>
            </v-card-text>
            <v-card-actions>
              <v-btn @click="freeDiscFunc()" x-small color="pink darken-2" text
                ><v-icon>mdi-refresh</v-icon>&nbsp;Refresh</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-col>

        <!-- System time -->
        <v-col xl="4" lg="4" sm="12" md="6" xs="24">
          <v-card elevation="2" outlined tile class="card-height">
            <v-card-title>
              <h5>SYSTEM TIME</h5>
              <v-row align="center" justify="end">
                <v-avatar size="35">
                  <img alt="logo" :src="logo" />
                </v-avatar>
              </v-row>
            </v-card-title>
            <v-card-text class="card-text-height">
              <div v-for="(key, idx) in objectKey(time)" :key="idx">
                {{ toUpper(key) }}:&nbsp;
                <span class="value-style" v-if="key == 'time_utc' || key == 'time_local'">{{
                  time[key] | formatDate
                }}</span>
                <span class="value-style" v-if="key != 'time_utc' && key != 'time_local'">{{ time[key] }}</span>
              </div>
            </v-card-text>
            <v-card-actions>
              <v-btn @click="getSystemTime()" x-small color="pink darken-2" text
                ><v-icon>mdi-refresh</v-icon>&nbsp;Refresh</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-col>

        <!-- System memory -->
        <v-col xl="4" lg="4" sm="12" md="6" xs="24">
          <v-card elevation="2" outlined tile class="card-height">
            <v-card-title>
              <h5>SYSTEM MEMORY</h5>
              <v-row align="center" justify="end">
                <v-avatar size="35">
                  <img alt="logo" :src="logo" />
                </v-avatar>
              </v-row>
            </v-card-title>
            <v-card-text class="card-text-height">
              <div v-for="(key, idx) in objectKey(memory)" :key="idx">
                {{ toUpper(key) }}:&nbsp;<span class="value-style">{{ memory[key] }}</span>
              </div>
            </v-card-text>
            <v-card-actions>
              <v-btn @click="getSystemMemory()" x-small color="pink darken-2" text
                ><v-icon>mdi-refresh</v-icon>&nbsp;Refresh</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </div>
  </v-card-text>
</template>

<script>
import axios from '../axios';
import PageTitle from './PageTitle';
import logo from '../serviceicons/nube-frog-green.png';
export default {
  components: { PageTitle },
  data: () => ({
    logo,
    freeDisc: {},
    time: {},
    memory: {},
    objectKey: Object.keys,
  }),
  async mounted() {
    this.freeDiscFunc();
    this.getSystemTime();
    this.getSystemMemory();
  },
  computed: {
    setting() {
      return this.$store.state.setting;
    },
  },
  methods: {
    freeDiscFunc() {
      axios.get(`rubix-service/disc`).then(data => {
        this.freeDisc = data;
      });
    },
    getSystemTime() {
      axios.get(`rubix-service/time`).then(data => {
        if (data.tz_local) {
          data.tz_local.forEach((element, index) => {
            const key = `tz_local_[${index}]`;
            data = { ...data, [key]: element };
          });
          delete data.tz_local;
        }
        this.time = data;
      });
    },
    getSystemMemory() {
      axios.get(`rubix-service/memory`).then(data => {
        this.memory = data;
      });
    },
    toUpper(str) {
      return str
        .toLowerCase()
        .split('_')
        .map(function (word) {
          return word[0].toUpperCase() + word.substr(1);
        })
        .join(' ');
    },
  },
};
</script>
