<template>
  <iframe frameBorder="0" :src="url" />
</template>

<script>

import axios from "../axios";

export default {
  name: "iframe",
  data:()=>({
    url: ''
  }),
  mounted(){
    const { id } = this.$route.params;
    this.getData(id);
  },
  watch: {
    '$route.params.id': function(val, oldVal) {
      this.getData(val);
    }
  },
  methods: {
    getData(id) {
      axios.get(`iframe/${id}`).then(res => {  
        if(res.ip==='localhost'){
          this.url=`http://${location.hostname}:${res.port}`;
          return;
        }  
        this.url = `http://${res.ip}:${res.port}`;
      })  
    },
  }
};
</script>
<style scoped>
  iframe {
    height: calc(100vh - 12px);
    width: calc(100% - 12px);
    float: left;
  }
</style>
