<template>
  <div style="padding:20px">
    <v-row>
       <v-col class="text-left">
        <v-btn x-small class="text-left" color="primary" @click="dialog = true; current = {show: true, isInternal: false}" >
          Create
          <v-icon small>mdi-plus</v-icon>
        </v-btn>
        <v-divider inset vertical></v-divider>
        <v-btn x-small color="primary" @click="clickImport" >
          Import&nbsp;<v-icon small>mdi-import</v-icon>
        </v-btn>
        <v-divider inset vertical></v-divider>
        <v-btn x-small @click="clickJsonExport" color="#009688" class="white--text">
          Export&nbsp;<v-icon small>mdi-export</v-icon>
        </v-btn>
      </v-col>
    </v-row>
    <v-data-table :headers="headers" :items="items">
      <template v-slot:item.icon="{item}">
        <v-icon>
          mdi-{{item.icon}}
        </v-icon>
      </template>
      <template v-slot:item.tools="{item}">
        <v-btn @click="() => toggleConfirmDialog(item)" icon small color="error" class="white--text">
          <v-icon small dark >
            mdi-delete
          </v-icon>
        </v-btn>
        <v-btn @click="() => clickUpdate(item)" icon small color="success" class="white--text">
          <v-icon small dark >
            mdi-rename-box
          </v-icon>
        </v-btn>
      </template>
    </v-data-table>
    <v-dialog v-model="dialog" max-width="500px">
      <service-form :onSave="onSave" :onCancel="onCancel" :item="current"></service-form>
    </v-dialog>
    <v-dialog v-model="confirmDialog" persistent max-width="290" >
      <v-card>
        <v-card-title class="headline">
          Confirm?
        </v-card-title>
        <v-card-text>Do you want to delete?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="closeDialog" >
            No
          </v-btn>
          <v-btn color="red darken-1" text @click="clickDeleteService()">
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
     <v-dialog v-model="importDialog" max-width="500px">
        <v-card>
          <v-card-title>
            <span class="headline">Import</span>
          </v-card-title>

          <v-card-text>
            <v-alert
              class="text-left"
              dense
              colored-border
              type="error"
              elevation="2"
              v-show="isValidData"
            >Incorrect Data Type!</v-alert>
            <v-container>
              <v-textarea
                outlined
                name="input-7-4"
                label="import data"
                v-model="dataImport"
                @input="onChangeImportData"
              ></v-textarea>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
            <v-btn color="blue darken-1" :disabled="disableSave" text v-on:click="saveImport">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
  </div>
</template>

<script>
import axios from "../axios";
import ServiceForm from './ServiceForm';
import { copyToClipboard , isJsonString, isValidObject, isValidArray } from '../utils/utils';

const requireKeys = ["show","name","releaseUrl","service","fromVersion","_id","isInternal","label","icon"];

export default {
    name: "View",
    components: {
      ServiceForm
    },
    data(){
      return{
          current: {},
          dialog:false,
          confirmDialog:false,
          choseService:{},
          importDialog:false,
          dataImport:"",
          isValidData : false,
          disableSave:true,
          headers: [
            {
              text: 'Label',
              align: 'start',
              value: 'label',
            },
            {
              text: 'Name',
              align: 'start',
              value: 'name',
            },
            { text: 'Icon', value: 'icon' },
            { text: 'Tools', value: 'tools' },
        ],
      };
    },
    computed: {
      items () {
        return this.$store.state.services;
      },
    },
    methods: {
      closeDialog(){
        this.confirmDialog = false;
        this.choseService={};
      },
      toggleConfirmDialog(data){
        this.choseService = data;
        this.confirmDialog = !this.confirmDialog;
      },
      getItems() {
        axios.get('service').then(r => {
          this.$store.commit('changeServices', r);
        })
      },
      async clickDeleteService(data) {
        this.$store.commit('changeLoading', true);
        try{
          await axios.delete(`service/${this.choseService._id}`)
          this.$store.commit('changeLoading', false);
          this.$toasted.show('Success!');
          this.getItems();
          this.closeDialog();

        }catch(message){
          this.$store.commit('changeLoading', false);
          this.$toasted.show(message, {type: 'error'});
        }
      },
      clickUpdate(data) {
        this.current = Object.assign({}, data);
        this.dialog= true;
      },
      onSave(data) {
        this.$store.commit('changeLoading', true);
        const method = data._id ? 'put': 'post';
        axios[method]('service', data).then(r => {
          this.$store.commit('changeLoading', false);
          this.$toasted.show('Success!');
          this.dialog = false;
          this.getItems();
        });
      },
      onCancel() {
          this.dialog = false;
      },
      async onChangeImportData(){
      this.isValidData = this.disableSave= !isJsonString(this.dataImport) || !isValidArray(this.dataImport, requireKeys);
      },
      clickJsonExport(){
        const status = copyToClipboard(JSON.stringify(this.items));
        const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
        const type = status ? `success` : `error`;
        return this.$toasted.show(msg, {type: type});
      },
      async saveImport(){
        const data =JSON.parse(this.dataImport);
        for(let i = 0 ; i < data.length ; i++ ){
          this.$store.commit('changeLoading', true);
          const method = data[i]._id ? 'put': 'post';
          await axios[method]('service',data[i]).then(r => {
            this.$store.commit('changeLoading', false);
            this.getItems();
            this.importDialog = false;
            this.dataImport = '';
          });
        }
      },
      closeDialogImport(){
        this.importDialog = false;
      },
      clickImport(){
        this.importDialog = true;
      },
  }
}
</script>

