<template>
  <v-card>
      <v-card-title>
        <span class="headline">Iframe</span>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-form v-model="valid" ref="form" lazy-validation>
            <v-row>
              <v-col>
                <v-text-field v-model="formData.name" label="Name" required :rules="[rules.required]"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col style="display:flex">
                <v-select v-model="formData.type" label="Type" :items="['linkOnly', 'iframeOnly', 'linkAndIframe']" required :rules="[rules.required]"></v-select>
                <v-checkbox label="Show?" v-model="formData.show" />
              </v-col>
              
            </v-row>
            <v-row>
              <v-col cols="10">
                 <v-text-field v-model="formData.ip" label="Ip" @input="onChangeInput" required :rules="[rules.required]"></v-text-field>
              </v-col>
              <v-col cols="2">
                <v-text-field v-model="formData.port" label="Port" required :rules="[rules.required]"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="6">
                <v-switch @change="onChangeIp"  v-model="enableip" label="Use Global Ip"></v-switch>
              </v-col>
              <v-col cols="6">
                <v-combobox
                  v-on:change="changeIcon"
                  :loading="loading"
                  :items="icons"
                  :search-input.sync="search"
                  label="Select Icon"
                  item-text="name"
                  item-value="name"
                  :value="formData.icon"
                  :rules="[rules.required]"
                >
                  <template slot="item" slot-scope="{item}">
                    <v-icon dark> {{ `mdi-${item.name}` }} </v-icon> <v-spacer></v-spacer>{{item.name}}
                  </template>
                </v-combobox>
              </v-col>
            </v-row>
          </v-form>
        </v-container>
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
        <v-btn color="blue darken-1" :disabled="!valid" text @click="preSave()"> Save </v-btn>
      </v-card-actions>
    </v-card>
</template>

<script>
  import axios from "../axios";

  export default {
    props: ['onSave', 'onCancel', 'item'],
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      loading: false,
      valid: false,
      enableip:true,
      icons: [],
      search: null,
      select: null,
      typingText:'',
      defaultIp:'',
      defaultItem: {},
      typingIp:''
    }),
    watch: {
      search (val) {
        val && val !== this.formData.icon && this.getIcons(val)
      }
    },
    computed: {
      formData () {
        return this.item;
      },
      setting(){
        return this.$store.state.setting;
      }
    },
    mounted(){      
      this.defaultItem = { ...this.item };
      this.defaultIp=this.item.ip;
      this.isAddOrUpdate();
    },
    methods: {
      onChangeInput(val){
        this.typingIp=val;
        if(val===this.setting.globalIp){
          this.enableip=true;
        }
      },
      preSave() {
        const isValid = this.$refs.form.validate();
        if (isValid && this.formData.icon) {
          this.onSave(this.formData);
          return;
        }
        this.$toasted.show('This icon isnt include in the icon list !',{ type : "error" } );
      },
      isAddOrUpdate(){
        if( Object.keys(this.defaultItem).length === 0 && this.enableip){
           this.item.ip=this.setting.globalIp;
           this.item.port='80';
           return;
        }
        if(this.item.ip===this.setting.globalIp){
            this.enableip=true;
            return;
        }
        this.enableip=false;
        this.typingIp=this.item.ip;
      },
      onChangeIp(){
        if(Object.keys(this.defaultItem).length === 0){
          if(!this.enableip){
            this.item.ip=this.typingIp;
            return;
          }
          this.item.ip=this.setting.globalIp;
        }
        if(!this.enableip){
            this.item.ip=this.typingIp;
            return;
        }
        this.item.ip=this.setting.globalIp;
      },
      changeIcon({name}){
        if (name) {
          this.formData.icon = name;
          return;
        }
        this.formData.icon='';
      },
      getIcons (v) {
        this.loading = true;
        axios.get('icons', {params: {keyword: v}}).then(r => {
          this.icons = r;
          this.loading = false;
        });
      }
    },
  };
</script>
