<template>
  <div>
    <v-data-table
      v-model="selected"
      :headers="headers"
      :items="items"
      class="elevation-1"
      item-key="uuid"
      show-select
      :footer-props="{
        showFirstLastPage: true,
        firstIcon: 'mdi-chevron-double-left',
        lastIcon: 'mdi-chevron-double-right',
        prevIcon: 'mdi-chevron-left',
        nextIcon: 'mdi-chevron-right'
      }"
      :search="search"
    >
      <template v-slot:top >
        <v-toolbar flat>
          <v-toolbar-title>BACNET SERVER</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div class="text-left pa-2">
            <v-btn @click="getItems" x-small color="primary" class="white--text" >
              Refresh
              <v-icon x-small right dark> mdi-refresh </v-icon>
            </v-btn>&nbsp;
            <v-btn @click="dialog = true; currentItem = null" x-small color="success" class="white--text" >
              Create
              <v-icon x-small right dark> mdi-plus </v-icon>
            </v-btn>&nbsp;
            <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text">
              Delete
              <v-icon x-small right dark > mdi-delete </v-icon>
            </v-btn>&nbsp;
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn x-small color="#009688" class="white--text"  dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
              </template>
              <v-list>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(items, 'json')">Json</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(items, 'excel')">Excel</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(items,'csv')">CSV</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>&nbsp;
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on" >Import<v-icon x-small right dark>mdi-import</v-icon></v-btn>
              </template>
              <v-list>
                <v-list-item link>
                  <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="dialogImportExcel = true; file = null">Excel</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>&nbsp;
            <v-btn @click="clickJsonExport(selected)" :disabled="selected.length === 0" x-small color="#009688" class="white--text" >
              Export
              <v-icon x-small right dark> mdi-export </v-icon>
            </v-btn>
          </div>
          <v-spacer></v-spacer>
          <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
        </v-toolbar>
      </template>

      <template v-slot:item.actions="{ item }" >
        <v-icon small class="mr-2" @click="editItem(item)" color="primary">
          mdi-pencil
        </v-icon>
        <v-icon small @click="clickDelete(item)" color="error">
          mdi-delete
        </v-icon>
      </template>
      <template v-slot:no-data> No data </template>
    </v-data-table>
    <!-- Delete dialog -->
    <v-dialog v-model="dialogDelete" persistent max-width="290">
      <v-card>
        <v-card-title> Confirm Delete</v-card-title>
        <v-card-text>
          <v-container>
            <v-row>Are you sure?</v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDelete"> No </v-btn>
          <v-btn color="red darken-1" text @click="confirm"> Yes </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- Import Json dialog-->
    <v-dialog v-model="dialogImport" >
      <v-toolbar flat dark >
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport" >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1" > Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2" > Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea
                    outlined
                    name="input-7-4"
                    label="import data"
                    v-model="dataImport"
                  ></v-textarea>

                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="headers"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="{
                  showFirstLastPage: true,
                  firstIcon: 'mdi-chevron-double-left',
                  lastIcon: 'mdi-chevron-double-right',
                  prevIcon: 'mdi-chevron-left',
                  nextIcon: 'mdi-chevron-right'
                }"
                :search="searchWizard"
              >
                <template v-slot:top >
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small @click="clickDeleteImport(item)" color="error"> mdi-delete </v-icon>
                </template>
                <template v-slot:item.object_name="{ item }" >
                  <v-text-field v-model="item.object_name" single-line style="width:100px" required :rules="[() => !isExistingvalue(item.object_name, 'object_name') || 'Name Existed', rules.required]"></v-text-field>
                </template>
                <template v-slot:item.address="{ item }" >
                  <v-text-field v-model="item.address" type="number" single-line style="width:60px" required :rules="[() => !isExistingvalue(item.address, 'address') || 'Address Existed', rules.required]"></v-text-field>
                </template>
                <template v-slot:item.description="{ item }" >
                  <v-text-field v-model="item.description" single-line style="width:250px"></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>
    <!-- Import Excel dialog-->
    <v-dialog v-model="dialogImportExcel" width="500px">
      <v-card>
        <v-card-title>Choose excel file</v-card-title>
        <v-card-text>
          <v-file-input outlined dense label="File input" v-model="file" accept=".xls,.xlsx"></v-file-input>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogImportExcel = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="convertExcelToJson" :disabled="!file">Convert</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- Create/edit dialog-->
    <v-dialog v-model="dialog" max-width="500px">
      <point-form :item="currentItem" :onCancel="onCancel" v-if="dialog" :onSave="onSave"></point-form>
    </v-dialog>
  </div>
</template>

<script>

import { exportCSV, isJsonString, copyToClipboard, exportExcel, exportJSON } from "../utils/utils";
import axios from "../axios";
import * as xlsx from "xlsx";
import PointForm from "./PointForm";

export default {
  components: {
    PointForm,
  },
  data:()=>({
    dialog: false,
    headers: [
      { text: "Actions", value: "actions", sortable: false },
      { text: "Object Name", value: "object_name", width:"125px" },
      { text: "Object Type", value: "object_type", sortable: false },
      { text: "Address", value: "address", sortable: false },
      { text: "Present Value ",value:"point_store.present_value", sortable: false },
      { text: "Present Value Last Update",value:"point_store.ts", sortable: false },
      { text: "Relinquish Default", value: "relinquish_default", sortable: false },
      { text: "Description", value: "description", sortable: false },
      { text: "Data Offset", value: "data_offset", sortable: false },
      { text: "Data Round", value: "data_round", sortable: false },
      { text: "Enable",value: "enable", sortable: false },
      { text: "Event State",value: "event_state", sortable: false },
      // { text: "Fault",value: "fault", sortable: false },
      { text: "Units", value: "units",align:"center", sortable: false },
      // { text: "Priority Array Write _1",value:"priority_array_write._1",align:"start", sortable: false },
      // { text: "Priority Array Write _2",value:"priority_array_write._2",align:"start", sortable: false },
      // { text: "Priority Array Write _3",value:"priority_array_write._3",align:"start", sortable: false },
      // { text: "Priority Array Write _4",value:"priority_array_write._4",align:"start", sortable: false },
      // { text: "Priority Array Write _5",value:"priority_array_write._5",align:"start", sortable: false },
      // { text: "Priority Array Write _6",value:"priority_array_write._6",align:"start", sortable: false },
      // { text: "Priority Array Write _7",value:"priority_array_write._7",align:"start", sortable: false },
      // { text: "Priority Array Write _8",value:"priority_array_write._8",align:"start", sortable: false },
      // { text: "Priority Array Write _9",value:"priority_array_write._9",align:"start", sortable: false },
      // { text: "Priority Array Write _10",value:"priority_array_write._10",align:"start", sortable: false },
      // { text: "Priority Array Write _11",value:"priority_array_write._11",align:"start", sortable: false },
      // { text: "Priority Array Write _12",value:"priority_array_write._12",align:"start", sortable: false },
      // { text: "Priority Array Write _13",value:"priority_array_write._13",align:"start", sortable: false },
      // { text: "Priority Array Write _14",value:"priority_array_write._14",align:"start", sortable: false },
      // { text: "Priority Array Write _15",value:"priority_array_write._15",align:"start", sortable: false },
      // { text: "Priority Array Write _16",value:"priority_array_write._16",align:"start", sortable: false },
      { text: "Created On",value:"created_on", sortable: false },
      { text: "Updated On",value: "updated_on" , sortable: false },
      { text: "UUID", value: "uuid", align: "start", sortable: false },
    ],
    selected: [],
    valid: false,
    importArray: [],
    dataImport: "",
    action: "",
    file: null,
    dialogImportExcel: false,
    e1: 1,
    dialogImport: false,
    dialogDelete: false,
    items: [],
    rules: {
      required: (value) => !!value || 'Required.',
    },
    search:"",
    searchWizard:"",
  }),
  // mounted() {
  //   this.getItems();
  // },
  watch: {
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  async mounted() {
    const { setting } = this.$store.state;
    this.getData();
    this.getItems();
  },
  methods: {
    getData() {
      this.getBacnetInfo();
    },
    editItem(item) {
      this.currentItem = item;
      this.dialog = true;
    },
    onCancel() {
      this.dialog = false;
    },
     async getBacnetInfo() {
      this.bacnetInfo = await axios.get(`bac-net/server-info`);
    },
    async getBacnetStatus() {
      this.bacnetServerStatus = await axios.get(`bac-net/server-status`);
    },
    async onBacNetSave(data) {
      await axios.patch(`bac-net/server-info`, data).then(r => {
        this.dialog = false;
        this.$toasted.show('Success');
        this.getBacnetInfo();
      })
    },
    async onSave(data) {
      let postData = {...data};
      const method = data.uuid ? 'patch': 'post';
      let url = `bac-net/point`;
      postData.enable = data.enable ? data.enable : false;
      if (data.uuid) {
        url += '/' + data.uuid;
        delete postData.uuid;
      }
      axios[method](url, postData).then(async value => {
        this.dialog = false;
        await this.getItems();
        this.$toasted.show("success");
      });

    },
    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.items.indexOf(this.currentItem);
      this.items.splice(this.editedIndex, 1);
      await axios.delete(`bac-net/point/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = "";
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    async getItems() {
      const { points } = await axios.get( `bac-net/point` );
      points.forEach(e=>{
          const keys=Object.keys(e.priority_array_write);
          const values=Object.values(e.priority_array_write);
          for(let i=0;i<keys.length;i++){
            if(values[i]===null){
                e.priority_array_write[keys[i]]='null'
            }
          }
      });
      this.items = points;
    },
    async saveImport() {
      if(this.importArray && this.importArray.length > 0){
        if(this.$refs.form.validate()){
          const promises = [];
          for (const item of this.importArray) {
            delete item.uuid;
            await this.onSave(item);
          }
          this.closeDialogImport();
          this.selected = [];
        }
      }
    },
    clickContinue(){
      if(!isJsonString(this.dataImport) ){
        return this.$toasted.show("incorrect data type!", { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport);
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName) {
      const points = _.cloneDeep(this.items);
      const index = this.items.indexOf(this.currentItem);
      if(index !== -1){ //when edit
        points.splice(index, 1);
      }
      if(fieldName === "address"){
        value = Number(value)
      }
      return _.find(points, {[fieldName]: value}) != null;;
    },
    clickImportJson(){
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : "";
      this.e1 = 1;
    },
    convertExcelToJson(){
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(this.file);
      fileReader.onload = (event)=>{
        let data = event.target.result;
        let workbook = xlsx.read(data,{type:"binary"});
        workbook.SheetNames.forEach(sheet => {
            let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
            const items = rowObject.map((i) => {
              for(let key in i){
                const newKey = key.trimRight("");
                const value = i[key];
                delete i[key];
                i = {...i, [newKey]: value}
                i.priority_array_write = this.getPriorityArrayWrite(i);
              }
              return i;
            });
            this.dataImport = JSON.stringify(items);
            this.dialogImportExcel = false;
            this.dialogImport = true;
            this.e1 = 1;
        });
      }
    },
    getPriorityArrayWrite(point){
      let priority_array_write = {};
      Object.keys(point).forEach(key =>{
        let keys = key.split('.');
        if(keys[1]==="priority_array_write"){
          priority_array_write = {...priority_array_write,[keys[2]]: point[key]}
        }
      })
      return priority_array_write;
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.$nextTick(() => {
        this.dataImport = "";
      });
    },
    clickDownload(array, type){
      const data = this.convertExportData(array);
      if(type === "csv"){
          return exportCSV("points", data);
      } if(type === "excel"){
          return exportExcel("points", data);
      } if(type === "json"){
          return exportJSON("points", JSON.stringify(this.items));
      }
    },
    clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    convertExportData(array){
      const data = _.cloneDeep(array).map((point)=>{
        _.forIn(point, (point_value, point_key) => {
          if(point_key == 'point_store' || point_key == 'priority_array_write'){
            _.forIn(point[point_key], (sub_value, sub_key) => {
              const subKey = `point.${point_key}.${sub_key}`;
              point = {...point, [subKey] : sub_value}
            });
            delete point[point_key];
          }
        });
        return point;
     });
      return data;
    },
    // getNotExistingElm(data, key) {
    //   const result = key === 'object_name'? `${data}_${this.version}` : data + this.version;
    //   const existingItem = this.items.find(item =>item[key] === result);
    //   if (!existingItem) {
    //     return result;
    //   }
    //   this.version++;
    //   return this.getNotExistingElm(data, key);
    // }
  }
}
</script>
