<template>
  <div>
    <v-data-table
      v-model="selected"
      :headers="pointHeaders"
      :items="device.points"
      class="elevation-1"
      item-key="uuid"
      show-select
      :item-class="backgroundColor"
      :footer-props="{
        showFirstLastPage: true,
        firstIcon: 'mdi-chevron-double-left',
        lastIcon: 'mdi-chevron-double-right',
        prevIcon: 'mdi-chevron-left',
        nextIcon: 'mdi-chevron-right',
      }"
      :search="search"
    >
      <template v-slot:top>
        <v-breadcrumbs :items="dirs" large>
          <v-breadcrumbs-item
            v-bind:class="{ 'cursor-pointer': !item.disabled }"
            slot="item"
            slot-scope="{ item }"
            @click.native="() => clickBreadcrumb(item)"
            :key="item.text"
          >
            {{ item.text }}
          </v-breadcrumbs-item>
        </v-breadcrumbs>
        <v-toolbar flat>
          <v-toolbar-title>POINTS</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div class="text-left pa-2">
            <v-btn @click="getData" x-small color="primary" class="white--text">
              Refresh<v-icon x-small right dark> mdi-refresh </v-icon> </v-btn
            >&nbsp;
            <v-btn @click="dialog = true" x-small color="success" class="white--text">
              Create<v-icon x-small right dark> mdi-plus </v-icon> </v-btn
            >&nbsp;
            <v-btn
              @click="clickDeleteMulti"
              :disabled="selected.length === 0"
              x-small
              color="error"
              class="white--text"
            >
              Delete<v-icon x-small right dark> mdi-delete </v-icon> </v-btn
            >&nbsp;
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn x-small color="#009688" class="white--text" dark v-bind="attrs" v-on="on"
                  >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn
                >
              </template>
              <v-list>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(device.points, 'json')">Json</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(device.points, 'excel')">Excel</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(device.points, 'csv')">CSV</v-list-item-title>
                </v-list-item>
              </v-list> </v-menu
            >&nbsp;
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on"
                  >Import<v-icon x-small right dark>mdi-import</v-icon></v-btn
                >
              </template>
              <v-list>
                <v-list-item link>
                  <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title
                    @click="
                      dialogImportExcel = true;
                      file = null;
                    "
                    >Excel</v-list-item-title
                  >
                </v-list-item>
              </v-list> </v-menu
            >&nbsp;
            <v-btn
              @click="clickJsonExport(selected)"
              :disabled="selected.length === 0"
              x-small
              color="#009688"
              class="white--text"
            >
              Export<v-icon x-small right dark> mdi-export </v-icon>
            </v-btn>
          </div>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-toolbar>
      </template>

      <template v-slot:item.actions="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)" color="primary"> mdi-pencil </v-icon>
        <v-icon small @click="clickDelete(item)" color="error"> mdi-delete </v-icon>
      </template>

      <template v-slot:no-data> No data </template>
    </v-data-table>

    <!-- Create/Edit dialog-->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field v-model="device.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    v-model="editedItem.name"
                    label="Point name"
                    required
                    :rules="[() => !isExistingvalue(editedItem.name, 'name') || 'Name Existed', rules.required]"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.timeout_global" label="timeout global"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select
                    v-model="editedItem.function_code"
                    label="function_code"
                    :items="point_types"
                    required
                    :rules="[rules.required]"
                    v-on:change="$refs.form.resetValidation()"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select
                    v-model="editedItem.data_type"
                    label="data type"
                    :items="point_data_types"
                    required
                    :rules="[rules.required]"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select
                    v-model="editedItem.data_endian"
                    label="data endian"
                    :items="point_data_endians"
                    required
                    :rules="[rules.required]"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    v-model="editedItem.register"
                    label="register"
                    :rules="[() => !isExistingvalue(editedItem.register, 'register', editedItem) || 'Register Existed']"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    v-model="editedItem.register_length"
                    label="register length"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.timeout" label="timeout" type="number"></v-text-field>
                </v-col>

                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.write_value" label="write value" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.cov_threshold" label="cov threshold" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field  value="Point round/math" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.value_round" label="value round" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.value_offset" label="value offset" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select
                    v-model="editedItem.value_operation"
                    label="value operation"
                    :items="value_operation"
                  ></v-select>
                </v-col>
              
                <v-col cols="12" sm="24" md="24">
                  <v-text-field  value="Point value scale" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.input_min" label="input_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.input_max" label="input_max" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.scale_min" label="scale_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.scale_max" label="scale_max" type="number"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import Json dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat dark>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="pointHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="{
                  showFirstLastPage: true,
                  firstIcon: 'mdi-chevron-double-left',
                  lastIcon: 'mdi-chevron-double-right',
                  prevIcon: 'mdi-chevron-left',
                  nextIcon: 'mdi-chevron-right',
                }"
                :search="searchWizard"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="searchWizard"
                      append-icon="mdi-magnify"
                      label="Search"
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small @click="clickDeleteImport(item)" color="error"> mdi-delete </v-icon>
                </template>
                <template v-slot:item.name="{ item }">
                  <v-text-field
                    v-model="item.name"
                    single-line
                    style="width: 100px"
                    required
                    :rules="[() => !isExistingvalue(item.name, 'name') || 'Name Existed', rules.required]"
                  ></v-text-field>
                </template>
                <template v-slot:item.register="{ item }">
                  <v-text-field
                    v-model="item.register"
                    type="number"
                    single-line
                    style="width: 100px"
                    required
                    :rules="[() => !isExistingvalue(item.register, 'register') || 'Register Existed', rules.required]"
                  ></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <v-dialog v-model="dialogImportExcel" width="500px">
      <v-card>
        <v-card-title>Choose excel file</v-card-title>
        <v-card-text>
          <v-file-input outlined dense label="File input" v-model="file" accept=".xls,.xlsx"></v-file-input>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogImportExcel = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="convertExcelToJson" :disabled="!file">Convert</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Delete dialog -->
    <v-dialog v-model="dialogDelete" persistent max-width="290">
      <v-card>
        <v-card-title> Confirm Delete Point</v-card-title>
        <v-card-text>
          <v-container>
            <v-row>Are you sure?</v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDelete"> No </v-btn>
          <v-btn color="red darken-1" text @click="confirm"> Yes </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { isJsonString, copyToClipboard, exportJSON, exportCSV, exportExcel, parseExcel } from '../utils/utils';
import axios from '../axios';
import * as xlsx from 'xlsx';

export default {
  data: () => ({
    objectKeys: Object.keys,
    objectValues: Object.values,
    editedIndex: -1,
    action: '',
    device: {},
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    pointHeaders: [
      { text: 'Actions', value: 'actions', sortable: false },
      { text: 'Name', value: 'name', width: '90px' },
      { text: 'Driver', value: 'driver', sortable: false },
      { text: 'Enable', value: 'enable', sortable: false },
      { text: 'Fault', value: 'fault', sortable: false },
      { text: 'Fault message', value: 'fault_message', sortable: false },
      { text: 'Fault timestamp', value: 'ts_fault', sortable: false },
      { text: 'Register', value: 'register', sortable: false },
      { text: 'Register length', value: 'register_length', sortable: false },
      { text: 'Function code', value: 'function_code', sortable: false },
      { text: 'Data type', value: 'data_type', sortable: false },
      { text: 'Data endian', value: 'data_endian', sortable: false },
      { text: 'Value', value: 'value', sortable: false },
      { text: 'Value original', value: 'value_original', sortable: false },
      { text: 'Last value change timestamp', value: 'ts_value', sortable: false },
      { text: 'Value round', value: 'value_round', sortable: false },
      { text: 'Value offset', value: 'value_offset', sortable: false },
      { text: 'Value operation', value: 'value_operation', sortable: false },
      { text: 'Value raw', value: 'value_raw', sortable: false },
      { text: 'Writable', value: 'writable', sortable: false },
      { text: 'Write value', value: 'write_value', sortable: false },
      { text: 'Device uuid', value: 'device_uuid', sortable: false },
      { text: 'History enable', value: 'history_enable', sortable: false },
      { text: 'History type', value: 'history_type', sortable: false },
      { text: 'History interval', value: 'history_interval', sortable: false },
      { text: 'COV threshold', value: 'cov_threshold', sortable: false },
      { text: 'Input min', value: 'input_min', sortable: false },
      { text: 'Input max', value: 'input_max', sortable: false },
      { text: 'Scale min', value: 'scale_min', sortable: false },
      { text: 'Scale max', value: 'scale_max', sortable: false },
      { text: 'Timeout', value: 'timeout', sortable: false },
      { text: 'Timeout global', value: 'timeout_global', sortable: false },
      { text: 'Created on', value: 'created_on', sortable: false },
      { text: 'Updated on', value: 'updated_on', sortable: false },
      { text: 'UUID', value: 'uuid', align: 'start', sortable: false },
    ],
    point_types: [
      'READ_COILS',
      'READ_DISCRETE_INPUTS',
      'READ_HOLDING_REGISTERS',
      'READ_INPUT_REGISTERS',
      'WRITE_COIL',
      'WRITE_REGISTER',
      'WRITE_COILS',
      'WRITE_REGISTERS',
    ],
    point_data_types: ['RAW', 'INT16', 'UINT16', 'INT32', 'UINT32', 'FLOAT', 'DOUBLE', 'DIGITAL'],
    point_data_endians: ['LEB_BEW', 'LEB_LEW', 'BEB_LEW', 'BEB_BEW'],
    value_operation: ['ADD', 'SUBTRACT', 'MULTIPLY', 'DIVIDE', 'BOOL_INVERT'],
    currentItem: {},
    editedItem: {
      name: '',
      register: 0,
      register_length: 1,
      function_code: 'READ_HOLDING_REGISTERS',
      enable: true,
      write_value: 0,
      data_type: 'RAW',
      data_endian: 'BEB_LEW',
      value_round: 1,
      value_offset: 0,
      value_operation: 'ADD',
      input_min: null,
      input_max: null,
      scale_min: null,
      scale_max: null,
      cov_threshold: 0,
      timeout: 0,
      timeout_global: false,
      device_uuid: '',
    },
    defaultItem: {
      name: '',
      register: 0,
      register_length: 1,
      function_code: 'READ_HOLDING_REGISTERS',
      enable: true,
      write_value: 0,
      data_type: 'RAW',
      data_endian: 'BEB_LEW',
      value_round: 1,
      value_offset: 0,
      value_operation: 'ADD',
      input_min: null,
      input_max: null,
      scale_min: null,
      scale_max: null,
      cov_threshold: 0,
      timeout: 1,
      timeout_global: false,
      device_uuid: '',
    },
    rules: {
      required: value => !!value || 'Required.',
    },
    dataImport: '',
    importArray: [],
    dialogImport: false,
    dirs: [{ text: '..', link: 'modbus' }],
    e1: 1,
    dialogImportExcel: false,
    file: null,
    search: '',
    searchWizard: '',
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Point' : 'Edit Point';
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {},
  mounted() {
    this.getData();
  },
  methods: {
    async getData() {
      await axios.get(`point-server/device/${this.$route.query.device_uuid}`).then(data => {
        this.device = data;
        this.editedItem.device_uuid = this.defaultItem.device_uuid = this.device.uuid;
        this.device.points = data.points.map(item => {
          const keys = this.objectKeys(item.point_store);
          if (keys && keys.length > 0) {
            keys.forEach(key => {
              item = { ...item, [key]: item.point_store[key] };
            });
          }
          delete item.point_store;
          delete item.point_uuid;
          return item;
        });
        if (this.dirs.length != 3) {
          this.getDir();
        }
      });
    },
    getDir() {
      axios.get(`point-server/network/${this.device.network_uuid}`).then(data => {
        (this.dirs = [{ text: '..', link: 'modbus' }]),
          this.dirs.push({ text: data.name, link: `devices?network_uuid=${data.uuid}` });
        this.dirs.push({ text: this.device.name, disabled: true });
      });
    },
    clickBreadcrumb(dir) {
      if (!dir.disabled) {
        this.$router.push(dir.link);
      }
    },

    editItem(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updatePoint(this.editedItem);
        } else {
          this.createPoint(this.editedItem);
        }
        this.close();
      }
    },

    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.device.points.indexOf(this.currentItem);
      this.device.points.splice(this.editedIndex, 1);
      await axios.delete(`point-server/point/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },

    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },

    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form.validate()) {
          const promises = [];
          for (const item of this.importArray) {
            delete item.uuid;
            item.device_uuid = this.device.uuid  //update to the current device_uuid
            item.register_length = item.register_length ? item.register_length : 0;
            await this.createPoint(item);
          }
          this.closeDialogImport();
          this.selected = [];
        }
      }
    },

    clickImportJson() {
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
    },
    convertExcelToJson() {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(this.file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.device_uuid = this.device.uuid;
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport);
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName, item) {
      let points = _.cloneDeep(this.device.points);
      if (this.editedIndex !== -1) {
        //when edit
        points.splice(this.editedIndex, 1);
      }
      if (fieldName === 'register') {
        value = Number(value);
        if (item) {
          points = points.filter(p => p.function_code === item.function_code);
        }
      }
      return _.find(points, { [fieldName]: value }) != null;
    },
    clickJsonExport() {
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      if (type === 'csv') {
        return exportCSV('points', array);
      }
      if (type === 'excel') {
        return exportExcel('points', array);
      }
      if (type === 'json') {
        return exportJSON('points', JSON.stringify(array));
      }
    },

    updatePoint(data) {
      axios.put(`point-server/point/${this.editedItem.uuid}`, data).then(response => {
        const index = this.device.points.findIndex(n => n.uuid == response.uuid);
        Object.assign(this.device.points[index], response);
        this.$toasted.show('update success');
      });
    },

    async createPoint(data) {
      await axios.post(`point-server/point`, data).then(response => {
        this.device.points.push(response);
        this.$toasted.show('create success');
      });
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'background-fault-true';
      } else {
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },
  },
};
</script>
<style scoped>
.centered-input >>> input {
  text-align: center;
}
.cursor-pointer {
  cursor: pointer;
  color: #1867c0;
}
.background-fault-true {
  background-color: #fbb93e;
}
.background-enable-false {
  background-color: #666666;
}
</style>
