<template>
  <div>
    <v-data-table
      v-model="selected"
      :headers="pointHeaders"
      :items="device.points"
      class="elevation-1"
      item-key="uuid"
      show-select
      :footer-props="{
        showFirstLastPage: true,
        firstIcon: 'mdi-chevron-double-left',
        lastIcon: 'mdi-chevron-double-right',
        prevIcon: 'mdi-chevron-left',
        nextIcon: 'mdi-chevron-right'
      }"
      :search="search"
    >
      <template v-slot:top >
        <v-breadcrumbs :items="dirs" large>
          <v-breadcrumbs-item
            v-bind:class="{ 'cursor-pointer': !item.disabled }"
            slot="item"
            slot-scope="{ item }"
            @click.native="() => clickBreadcrumb(item)"
            :key="item.text">
            {{ item.text }}
          </v-breadcrumbs-item>
        </v-breadcrumbs>
        <v-toolbar flat>
          <v-toolbar-title>LORA NETWORK POINTS</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div class="text-left pa-2">
            <v-btn @click="getItems" x-small color="primary" class="white--text" >
              Refresh
              <v-icon x-small right dark> mdi-refresh </v-icon>
            </v-btn>&nbsp;
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn x-small color  ="#009688" class="white--text"  dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
              </template>
              <v-list>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(device.points, 'json')">Json</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(device.points, 'excel')">Excel</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(device.points,'csv')">CSV</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>&nbsp;
            <v-btn @click="clickJsonExport(selected)" :disabled="selected.length === 0" x-small color="#009688" class="white--text" >
              Export<v-icon x-small right dark> mdi-export </v-icon>
            </v-btn>
          </div>
          <v-spacer></v-spacer>
          <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
        </v-toolbar>
      </template>
      <template v-slot:no-data> No data </template>
    </v-data-table>
  </div>
</template>

<script>
import { isJsonString, copyToClipboard, exportJSON, exportCSV, exportExcel, parseExcel } from "../utils/utils";
import axios from "../axios";
import * as xlsx from "xlsx";

export default {
  data: () => ({
    editedIndex: -1,
    device: {},
    selected: [],
    pointHeaders: [
      { text: "Name", value: "name", width: "90px",},
      { text: "COV threshold", value: "cov_threshold", sortable: false},
      { text: "Enable", value: "enable", sortable: false},
      { text: "Fault", value: "fault", sortable: false},
      { text: "Fault message", value: "fault_message", sortable: false},
      { text: "Value", value: "value", sortable: false},
      { text: "Last value change timestamp", value: "ts", sortable: false},
      { text: "Value operation", value: "value_operation", sortable: false},
      { text: "Value operation", value: "value_operation", sortable: false},
      { text: "Value original", value: "value_original", sortable: false},
      { text: "Value raw", value: "value_raw", sortable: false},
      { text: "Value round", value: "value_round", sortable: false},
      { text: "Input max", value: "input_max", sortable: false},
      { text: "Input min", value: "input_min", sortable: false},
      { text: "Scale max", value: "scale_max", sortable: false},
      { text: "Scale min", value: "scale_min", sortable: false},
      { text: "Created on", value: "created_on", sortable: false},
      { text: "Updated on", value: "updated_on", sortable: false},
      { text: "Device point name", value: "device_point_name", sortable: false},
      { text: "Device UUID", value: "device_uuid", sortable: false},
      { text: "UUID", value: "uuid", sortable: false},
    ],
    dirs: [{text: '..', link: 'loRa-network'}],
    file: null,
    search:"",
  }),
  computed: {
  
  },
  watch: {
  
  },
  created() {
  },
  mounted() {
    this.getItems();
  },
  methods: {
    async getItems() {
      await axios.get(`lora/device/${this.$route.query.device_uuid}`).then((data) => {
        this.device = data;
        this.device.points = data.points.map((item) => {
          const keys = Object.keys(item.point_store);
          if (keys && keys.length > 0) {
            keys.forEach((key) => {
              item = { ...item, [key]: item.point_store[key] };
            });
          }
          delete item.point_store;
          delete item.point_uuid;
          return item;
        });
        
        //get dir
        if(this.dirs.length != 2){
          this.dirs = [{text: '..', link: 'loRa-network'}],
          this.dirs.push({text: this.device.name, disabled: true})
        }
      });
    },

    clickBreadcrumb(dir) {
      if(!dir.disabled){
        this.$router.push(dir.link);
      }
    },

    clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    clickDownload(array, type){
      if(type === "csv"){
          return exportCSV("LoRa Networks - Points", array);
      } if(type === "excel"){
          return exportExcel("LoRa Networks - Points", array);
      } if(type === "json"){
          return exportJSON("LoRa Networks - Points", JSON.stringify(array));
      }
    },

  },
};
</script>
<style scoped>
  .centered-input >>> input {
      text-align: center
  }
  .cursor-pointer{
    cursor: pointer;
    color: #1867c0;
  }
  .background-fault-true{
    background-color: #fbb93e;
  }
  .background-enable-false{
    background-color: #666666;
  }
</style>
